var React = require('react-external');

var ScreenActions = require('../../actions/ScreenActions');
var ModalActions = Panneau.Actions.Modal;
var Requests = require('../../requests/index');

var PlaylistItemAdd = React.createClass({
    
    propTypes: {
        bubblesSelectChannelsParams: React.PropTypes.object,
        currentItems: React.PropTypes.array
    },
    
    getDefaultProps: function() {
        return {
            bubblesSelectChannelsParams: null,
            currentItems: null
        };
    },
    
    getInitialState: function()
    {
        return {
            bubblesToAdd: null,
            filtersToAdd: null
        };
    },

    render: function()
    {
        return (
            <div className="list-item list-item-add">
                <a href="#" className="thumbnail" onClick={this.onClick}>
                    <span className="glyphicon glyphicon-plus"></span>
                    <strong>
                        Ajouter des diapositives
                    </strong>
                </a>
            </div>
        );

    },
    
    onClick: function(e)
    {
        e.preventDefault();
        
        this.setState({
            filtersToAdd: null,
            bubblesToAdd: null
        }, function()
        {
            Panneau.dispatch(ModalActions.openModal('BubblesSelect', {
                channelsParams: this.props.bubblesSelectChannelsParams,
                onComplete: _.bind(this.onBubblesModalComplete, this)
            }));
        });
        
        
    },
    
    onBubblesModalComplete: function(result)
    {
        Panneau.dispatch(ModalActions.closeModal('BubblesSelect'));
        
        var bubbles = _.get(result, 'bubbles', null);
        var filters = _.get(result, 'filters', null);
        
        var playlist_id = _.get(this.props, 'playlist.id');
        if(filters)
        {
            Requests.Playlists.addFilters(playlist_id, filters)
                .then(_.bind(this.onItemAdded, this));
        }
        else
        {
            Requests.Playlists.addBubbles(playlist_id, bubbles)
                .then(_.bind(this.onItemAdded, this));
        }
        
        /*this.setState({
            filtersToAdd: filters,
            bubblesToAdd: bubbles
        }, function()
        {
            
            Panneau.dispatch(ModalActions.openModal('Conditions', {
                bubbles: bubbles,
                filters: filters,
                onComplete: _.bind(this.onConditionModalComplete, this)
            }));
        });*/
    },
    
    onConditionModalComplete: function(condition)
    {
        var playlist_id = _.get(this.props, 'playlist.id');
        if(this.state.bubblesToAdd)
        {
            Requests.Playlists.addBubblesWithCondition(playlist_id, this.state.bubblesToAdd, condition)
                .then(_.bind(this.onItemAdded, this));
        }
        else if(this.state.filtersToAdd)
        {
            Requests.Playlists.addFiltersWithCondition(playlist_id, this.state.filtersToAdd, condition)
                .then(_.bind(this.onItemAdded, this));
        }
    },
    
    onItemAdded: function(items)
    {
        Panneau.dispatch(ModalActions.closeModal('Conditions'));
        
        items = !_.isArray(items) ? [items]:items;
        this.setState({
            filtersToAdd: null,
            bubblesToAdd: null
        }, function()
        {
            this.props.onAdd(items);
        });
    }
    
});

module.exports = PlaylistItemAdd;



