var React = require('react-external');
var _ = require('lodash-external');
var Immutable = require('immutable-external');

var FilterField = require('./Filter.react');

var FiltersField = React.createClass({
    
    propTypes: {
        name: React.PropTypes.string,
        onChange: React.PropTypes.func,
        value: React.PropTypes.array
    },
    
    getDefaultProps: function()
    {
        return {
            name: 'filters',
            onChange: null,
            value: null,
            filters: [
                {
                    'name': 'date',
                    'type': 'date',
                    'label': 'Date'
                },
                {
                    'name': 'author',
                    'type': 'select',
                    'label': 'Auteur',
                    'values': [
                        {
                            'value': '1',
                            'label': 'Auteur 1'
                        }
                    ]
                }
            ]
        };
    },
    
    render: function()
    {
        
        var value = _.get(this.props, 'value', null) || [];
        if(!value.length)
        {
            value.push({});
        }
        var filters = this.renderFilters(value);
        
        return (
            <div className="form-group form-group-list form-group-filters">
                { filters }
            </div>
        );
    },
    
    renderFilters: function(filters)
    {
        var lastIndex = filters.length-1;
        return filters.map(_.bind(function(filter, index)
        {
            var isLast = index === lastIndex;
            return this.renderFilter(filter, index, isLast);
        }, this));    
    },
    
    renderFilter: function(filter, index, isLast)
    {
        var key = 'filter_'+index;
        var name = this.props.name+'['+index+']';
        
        //Event handlers
        var onChange = _.bind(function(value)
        {
            this.onFilterChange(value, index);
        }, this);
        
        var onClickRemove = _.bind(function(e)
        {
            this.onFilterClickRemove(e, index);
        }, this);
        
        var addButton = null;
        if(isLast)
        {
            addButton = (
                <button type="button" className="btn btn-default glyphicon glyphicon-plus-sign" onClick={ this.onClickAdd }></button>
            );
        }
        
        var filters = this.props.filters;
        
        return (
            <div key={ key } className="form-group-list-item form-group-row">
                <div className="form-group-inline">
                    <FilterField filters={filters} name={name} value={filter} onChange={ onChange } />
                </div>
                <div className="form-group-inline form-group-actions">
                    <div className="btn-group">
                        { addButton }
                        <button type="button" className="btn btn-default glyphicon glyphicon-minus-sign" onClick={ onClickRemove }></button>
                    </div>
                </div>
            </div>
        );
        
    },
    
    onClickAdd: function(e)
    {
        e.preventDefault();
        
        var value = _.get(this.props, 'value', null) || [];
        if(!value.length)
        {
            value.push({});
        }
        value.push({});
        
        if(this.props.onChange)
        {
            this.props.onChange(value);
        }
    },
    
    onFilterChange: function(value, index)
    {
        var currentValue = Immutable.fromJS(this.props.value || []);
        var newValue = currentValue.set(index, value);
        if(newValue !== currentValue)
        {
            var value = newValue.toJS();
            
            if(this.props.onChange)
            {
                this.props.onChange(value);
            }
        }
    },
    
    onFilterClickRemove: function(e, index)
    {
        e.preventDefault();
        
        var value = _.get(this.props, 'value', null) || [];
        var currentValue = Immutable.fromJS(value);
        var newValue = currentValue.delete(index);
        value = newValue.toJS();
        
        if(this.props.onChange)
        {
            this.props.onChange(value);
        }
    }
    
    
});

module.exports = FiltersField;



