var graphql = require('./graphql');
var _ = require('lodash-external');
var Fragments = require('./Fragments');

module.exports = {
    
    updateAvatar: function(user, picture)
    {
        var user_id = _.isObject(user) ? _.get(user, 'id'):user;
        var picture_id = _.isObject(picture) ? _.get(picture, 'id'):picture;
        
        return graphql(`
                mutation Mutation<queryArgs>{
                    data: userUpdateAvatar<objectArgs>
                    {
                        ...userFields
                    }
                }
            `, {
                user_id: user_id+'',
                picture_id: picture_id+''
            }, {
                argsTypes: {
                    user_id: 'String',
                    picture_id: 'String'
                },
                fragments: [
                    Fragments.userFields
                ]
            }).then(function(result)
            {
                return result.data;
            });
    },
    
    removeAvatar: function(user)
    {
        var user_id = _.isObject(user) ? _.get(user, 'id'):user;
        
        return graphql(`
                mutation Mutation<queryArgs>{
                    data: userRemoveAvatar<objectArgs>
                    {
                        ...userFields
                    }
                }
            `, {
                user_id: user_id+''
            }, {
                argsTypes: {
                    user_id: 'String'
                },
                fragments: [
                    Fragments.userFields
                ]
            }).then(function(result)
            {
                return result.data;
            });
    }
    
};



