var React = require('react-external');
var _ = require('lodash-external');
var Immutable = require('immutable-external');


var Filter = React.createClass({
    
    propTypes: {
        name: React.PropTypes.string,
        onChange: React.PropTypes.func,
        value: React.PropTypes.object,
        filters: React.PropTypes.array
    },
    
    getDefaultProps: function()
    {
        return {
            name: 'filter',
            onChange: null,
            value: null,
            filters: []
        };
    },
    
    render: function()
    {
        var filterName = _.get(this.props, 'value.name', null);
        var filter = _.find(this.props.filters, 'name', filterName);
        var filterValue = _.get(this.props, 'value.value', null);
        var filterLabel = filter ? filter.label:'Filtrer par';
        var filtersMenu = this.renderFiltersMenu(this.props.filters|| []);
        var filterFieldName = this.props.name+'[name]';
        var valueFieldName = this.props.name+'[value]';
        
        var filterFieldGroup = null;
        if(filter)
        {
            var filterType = 'filter'+filter.type.replace(/[^a-z0-9]/gi,'').toLowerCase();
            var FilterField = _.find(Panneau.Components.Fields, function(Component, key)
            {
                return key.replace(/[^a-z0-9]/gi,'').toLowerCase() === filterType;
            });
            if(FilterField)
            {
                var other = _.omit(filter, ['name', 'value', 'label', 'onChange']);
                filterFieldGroup = (
                    <div className="form-group-inline">
                        <FilterField name={valueFieldName} value={filterValue} onChange={ this.onFilterValueChange } {...other} />
                    </div>
                );
            }
        }
        
        return (
            <div className="form-group form-group-filter form-group-row">
                <div className="form-group-inline">
                    <button type="button" className="btn btn-default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        <span className="tag">{ filterLabel }</span>
                        <span className="glyphicon glyphicon glyphicon-menu-down"></span>
                    </button>
                    <ul className="dropdown-menu">
                        { filtersMenu }
                    </ul>
                    <input type="hidden" name={filterFieldName} value={filterName} />
                </div>
                { filterFieldGroup }
            </div>
        );
    },
    
    renderFiltersMenu: function(filters)
    {
        return filters.map(_.bind(this.renderFiltersMenuItem, this));
    },
    
    renderFiltersMenuItem: function(filter, index)
    {
        var key = 'filter_menu'+index;
        
        var onClick = _.bind(function(e)
        {
            this.onFilterMenuClick(e, filter);
        }, this);
        
        return (
            <li key={key}>
                <a href="#" onClick={onClick}>{ filter.label }</a>
            </li>
        );
    },
    
    onFilterMenuClick: function(e, filter)
    {
        e.preventDefault();
        
        this.onValueChange({
            name: filter.name
        });
    },
    
    onFilterValueChange: function(value)
    {
        this.onValueChange('value', value);
    },
    
    onValueChange: function(name, value)
    {
        var currentValue = Immutable.fromJS(this.props.value || {});
        var newValue = _.isObject(name) ? Immutable.fromJS(name):currentValue.set(name, value);
        
        if(currentValue !== newValue)
        {
            var value = newValue.toJS();
            
            if(this.props.onChange)
            {
                this.props.onChange(value);
            }
        }
    }
    
});

