var React = require('react-external');

var ListActions = Panneau.Actions.List;

var TeamAdd = require('../items/TeamAdd.react');
var TeamItem = require('../items/Team.react');

var TeamList = React.createClass({
    
    mixins: [Panneau.Mixins.List],
    
    getDefaultProps: function()
    {
        return {
            canAdd: false,
            canEdit: false
        };
    },

    render: function()
    {
        var items = this.renderListItems(this.props.items);
        
        var addItem = null;
        if(this.props.canAdd)
        {
            addItem = (
                <TeamAdd onAdd={this.onAdd} />
            );
        }
        
        return (
            <div className="list list-thumbnails list-team">
                { items }
                { addItem }
            </div>
        );

    },
    
    getListItemComponent: function()
    {
        return TeamItem;
    },
    
    getListItemProps: function(it, index, itemProps)
    {
        var props = {};
        props.editable = this.props.canEdit,
        props.onUpdate = _.bind(function(user)
        {
            this.onItemUpdate(index, user);
        }, this);
        props.onRemove = _.bind(function()
        {
            this.onItemRemove(index);
        }, this);
        return props;
    },
    
    onAdd: function(invitation)
    {
        invitation.id = 'invitation'+invitation.id;
        Panneau.dispatch(ListActions.addItemIfUnique(invitation, 'id', this.props.name));
    },
    
    onItemUpdate: function(index, user)
    {
        Panneau.dispatch(ListActions.updateItem(index, user, this.props.name));
    },
    
    onItemRemove: function(index)
    {
        Panneau.dispatch(ListActions.removeItem(index, this.props.name));
    }

});

module.exports = TeamList;



