var graphql = require('./graphql');
var _ = require('lodash-external');

var Fragments = require('./Fragments');

module.exports = {
    
    loadForChannel: function(channel, params)
    {
        var id = _.isObject(channel) ? channel.id:channel;
        if(typeof(params) === 'undefined' || !params)
        {
            params = {};
        }
        params.channel_id = id;
        
        return graphql(`
            query Query<queryArgs>{
                data: bubblesPaginated<objectArgs>
                {
                    pagination {
                        ...paginationFields
                    }
                    items {
                        ...bubbleFields
                    }
                }
            }
        `, params, {
            argsTypes: {
                page: 'Int'
            },
            fragments: [Fragments.paginationFields, Fragments.bubbleFields]
        }).then(function(result)
        {
            return result.data;
        });
    }
    
};



