var graphql = require('./graphql');
var _ = require('lodash-external');

var Fragments = require('./Fragments');

module.exports = {
    
    addChannel: function(screen, channel)
    {
        var screen_id = _.isObject(screen) ? _.get(screen, 'id'):screen;
        var channel_id = _.isObject(channel) ? _.get(channel, 'id'):channel;
        
        return graphql(`
                mutation Mutation<queryArgs>{
                    data: screenAddChannel<objectArgs>
                    {
                        ...channelFields
                    }
                }
            `, {
                channel_id: channel_id,
                screen_id: screen_id
            }, {
                argsTypes: {
                    screen_id: 'String!',
                    channel_id: 'String!'
                },
                fragments: [Fragments.channelFields]
            }).then(function(result)
            {
                return result.data;
            });
    },
    
    removeChannel: function(screen, channel)
    {
        var screen_id = _.isObject(screen) ? _.get(screen, 'id'):screen;
        var channel_id = _.isObject(channel) ? _.get(channel, 'id'):channel;
        
        return graphql(`
                mutation Mutation<queryArgs>{
                    data: screenRemoveChannel<objectArgs>
                    {
                        ...channelFields
                    }
                }
            `, {
                channel_id: channel_id,
                screen_id: screen_id
            }, {
                argsTypes: {
                    screen_id: 'String!',
                    channel_id: 'String!'
                },
                fragments: [Fragments.channelFields]
            }).then(function(result)
            {
                return result.data;
            });
    },
    
    saveChannelSettings: function(screen, channel, settings)
    {
        var screen_id = _.isObject(screen) ? _.get(screen, 'id'):screen;
        var channel_id = _.isObject(channel) ? _.get(channel, 'id'):channel;
        
        var params = {
            screen_id: screen_id,
            channel_id: channel_id,
            settings: JSON.stringify(settings)
        };
        
        return graphql(`
                mutation Mutation<queryArgs>{
                    data: screenSaveChannelSettings<objectArgs>
                    {
                        ...screenChannelFields
                    }
                }
            `, params, {
                argsTypes: {
                    screen_id: 'String!',
                    channel_id: 'String!',
                    settings: 'String'
                },
                fragments: [Fragments.screenChannelFields]
            }).then(function(result)
            {
                return result.data;
            });
    },
    
};



