var React = require('react-external');
var ReactDOM = require('react-dom-external');

var TeamActions = require('../../actions/TeamActions');
var ModalActions = Panneau.Actions.Modal;

var TeamAddItem = React.createClass({
    
    getDefaultProps: function() {
        return {
            
        };
    },

    render: function()
    {
        return (
            <div className="list-item list-item-sm list-item-add">
                <a href={ URL.route('organisation.team.create') } className="thumbnail thumbnail-small thumbnail-horizontal" onClick={this.onClick}>
                    <span className="icon">
                        <span className="glyphicon glyphicon-plus"></span>
                    </span>
                    <span className="caption caption-lg">
                        Ajouter un membre de l'quipe
                    </span>
                </a>
            </div>
        );

    },
    
    onClick: function(e)
    {
        e.preventDefault();
        
        Panneau.dispatch(ModalActions.openModal('TeamInvite', {
            element: ReactDOM.findDOMNode(this),
            onComplete: _.bind(this.onModalComplete, this),
            onClose: _.bind(this.onModalClose, this)
        }));
    },
    
    onModalComplete: function(invitation)
    {
        if(this.props.onAdd)
        {
            this.props.onAdd(invitation);
        }
    },
    
    onModalClose: function()
    {
        
    }
    
});

module.exports = TeamAddItem;



