var React = require('react-external');
var ReactDOM = require('react-dom-external');
var _ = require('lodash-external');
var Panneau = require('panneau');
var Promise = require('promise');

var loadGMap = require('../lib/gmap').loadGMap;

var Map = React.createClass({
    
    propTypes: {
        latitude: React.PropTypes.number,
        longitude: React.PropTypes.number,
        zoom: React.PropTypes.number,
        radius: React.PropTypes.number,
        marker: React.PropTypes.bool
    },
    
    map: null,
    marker: null,
    circle:null,
    
    getDefaultProps: function()
    {
        return {
            apiKey: null,
            latitude: null,
            longitude: null,
            defaultLatitude: 45.509247,
            defaultLongitude: -73.56816,
            zoom: 14,
            radius: 0,
            marker: true,
            draggable: true
        };
    },
    
    render: function()
    {
        
        return (
            <div className="map-container">
                <div ref="map" className="map"></div>
            </div>
        );
    },
    
    renderMap: function()
    {
        var center = this.getCenter();
        var mapElement = ReactDOM.findDOMNode(this.refs.map);
        this.map = new google.maps.Map(mapElement, {
            center: center,
            zoom: this.props.zoom
        });
        
        if(this.props.radius)
        {
            this.renderCircle(center);
        }
        
        if(this.props.marker)
        {
            this.renderMarker(center);
        }
    },
    
    renderMarker: function(position)
    {
        if(this.marker)
        {
            this.marker.setMap(this.map);
            return;
        }
        
        this.marker = new google.maps.Marker({
            position: position,
            map: this.map,
            draggable: this.props.draggable
        });
        
        this.marker.addListener('drag', _.bind(this.onMarkerDrag, this));
        this.marker.addListener('dragend', _.bind(this.onMarkerDragEnd, this));
    },
    
    renderCircle: function(center)
    {
        if(this.circle)
        {
            this.circle.setMap(this.map);
            this.circle.setRadius(this.props.radius);
            return;
        }
        
        this.circle = new google.maps.Circle({
            strokeColor: '#0000FF',
            strokeOpacity: 0.8,
            strokeWeight: 2,
            fillColor: '#0000FF',
            fillOpacity: 0.35,
            center: center,
            map: this.map,
            radius: this.props.radius,
            editable: true,
            draggable: this.props.draggable
        });
        
        this.circle.addListener('radius_changed', _.bind(this.onCircleRadiusChanged, this));
        this.circle.addListener('drag', _.bind(this.onCircleDrag, this));
        this.circle.addListener('dragend', _.bind(this.onCircleDragEnd, this));
        
    },
    
    getCenter: function()
    {
        return {
            lat: this.props.latitude || this.props.defaultLatitude,
            lng: this.props.longitude || this.props.defaultLongitude
        };
    },
    
    componentDidMount: function()
    {
        loadGMap(this.props.apiKey).then(_.bind(this.renderMap, this));
    },
    
    componentWillUnmount: function()
    {
        this.map = null;
    },
    
    componentDidUpdate: function(prevProps, prevState)
    {
        if(typeof(google) === 'undefined' || typeof(google.maps) === 'undefined')
        {
            return;
        }
        
        var center = this.getCenter();
        
        if(this.props.latitude && this.props.longitude && (prevProps.latitude !== this.props.latitude || prevProps.longitude !== this.props.longitude))
        {
            if(this.map)
            {
                this.map.setCenter(center);
            }
            if(this.marker)
            {
                this.marker.setPosition(center);
            }
            if(this.circle)
            {
                this.circle.setCenter(center);
            }
        }
        if(prevProps.zoom !== this.props.zoom)
        {
            if(this.map)
            {
                this.map.setZoom(this.props.zoom);
            }
        }
        if(prevProps.marker !== this.props.marker)
        {
            if(this.props.marker)
            {
                this.renderMarker();
            }
            else if(this.marker)
            {
                this.marker.setMap(null);
            }
        }
        if(prevProps.radius !== this.props.radius)
        {
            if(this.props.radius)
            {
                this.renderCircle(center);
            }
            else if(this.circle)
            {
                this.circle.setMap(null);
            }
        }
    },
    
    onCircleRadiusChanged: function()
    {
        var radius = this.circle.getRadius();
        if(radius !== parseInt(this.props.radius))
        {
            if(this.props.onRadiusChange)
            {
                this.props.onRadiusChange(radius);
            }
        }
    },
    
    onCircleDrag: function(e)
    {
        if(this.marker)
        {
            this.marker.setPosition(this.circle.getCenter());
        }
    },
    
    onCircleDragEnd: function(e)
    {
        var position = this.circle.getCenter();
        if(this.props.onPositionChange)
        {
            this.props.onPositionChange({
                latitude: position.lat(),
                longitude: position.lng()
            })
        }
    },
    
    onMarkerDrag: function(e)
    {
        if(this.circle)
        {
            this.circle.setCenter(e.latLng);
        }
    },
    
    onMarkerDragEnd: function(e)
    {
        if(this.props.onPositionChange)
        {
            this.props.onPositionChange({
                latitude: e.latLng.lat(),
                longitude: e.latLng.lng()
            })
        }
    }
    
    
});

module.exports = Map;



