var React = require('react-external');

var ModalActions = Panneau.Actions.Modal;

var ChannelAddItem = React.createClass({
    
    getDefaultProps: function() {
        return {
            channelsAdded: [],
        };
    },

    render: function()
    {
        return (
            <div className="list-item list-item-xs list-item-add">
                <a href="#" className="thumbnail" onClick={this.onClick}>
                    <span className="icon">
                        <span className="glyphicon glyphicon-plus"></span>
                    </span>
                    <span className="caption">Ajouter une chane</span>
                </a>
            </div>
        );

    },
    
    onClick: function(e)
    {
        e.preventDefault();
        
        Panneau.dispatch(ModalActions.openModal('ChannelAdd', {
            channelsAdded: this.props.channelsAdded,
            onAdd: this.onModalAdd,
            onRemove: this.onModalRemove,
            onClose: this.onModalClose
        }));
    },
    
    onModalAdd: function(channel)
    {
        if(this.props.onAdd)
        {
            this.props.onAdd(channel);
        }
    },
    
    onModalRemove: function(channel)
    {
        if(this.props.onRemove)
        {
            this.props.onRemove(channel);
        }
    },
    
    onModalClose: function()
    {
        
    }
    
});

module.exports = ChannelAddItem;



