var React = require('react-external');
var ReactDOM = require('react-dom-external');
var Relay = Panneau.Relay;

var $ = require('jquery-external');

var ModalMixins = Panneau.Mixins.Modal;
var Modal = Panneau.Components.Modals.Modal;
var BubblesList = require('../lists/Bubbles.react');
var BubblesSelector = require('../BubblesSelector.react');

var BubblesSelectModal = React.createClass({
    
    propTypes: {
        visible: React.PropTypes.bool,
        channelsParams: React.PropTypes.object
    },
    
    getDefaultProps: function()
    {
        return {
            visible: false,
            channelsParams: null
        };
    },
    
    getInitialState: function()
    {
        return {
            selectedBubbles: [],
            filters: [],
            channel: null
        };
    },

    render: function()
    {
        
        var addSelectedButton = null;
        var bubblesCount = this.state.selectedBubbles.length;
        if(!bubblesCount)
        {
            addSelectedButton= (
                <button type="button" className="btn btn-default" disabled="disabled">
                    Slectionnez des diapositives...
                </button>
            );
        }
        else
        {
            var label = bubblesCount === 1 ? 'Ajouter 1 diapositive':'Ajouter les '+bubblesCount+' diapositives';
            addSelectedButton= (
                <button type="button" className="btn btn-default" onClick={this.onAddSelectedClick}>
                    { label }
                </button>
            );
        }
        
        var addFilterButtonProps = {
            type: 'button',
            className: 'btn btn-default',
            onClick: this.onAddFilterClick
        };
        if(!_.get(this.state, 'filters.0.value'))
        {
            addFilterButtonProps.disabled = 'disabled';
        }
        var addFilterButton = (
            <button {...addFilterButtonProps}  >
                Ajouter en tant que rgle
            </button>
        );
        
        
        return (
            <Modal closeModal={this.props.closeModal} visible={this.props.visible} onClose={this.onClose} onOpen={this.onOpen}>
                <div className="modal-header">
                    <button type="button" className="close" onClick={this.onClickClose} aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                    <h4 className="modal-title">Ajouter une diapositive</h4>
                </div>
                <div className="modal-body">
                    <BubblesSelector 
                        channelsParams={this.props.channelsParams} 
                        filters={this.state.filters} 
                        checkedBubbles={this.state.selectedBubbles} 
                        onChannelLoaded={this.onLoaded}
                        onBubblesLoaded={this.onLoaded}
                        onChange={this.onBubblesChange} 
                        onClickAdd={this.onBubbleClickAdd}
                        onChannelChange={this.onChannelChange}
                        onFiltersChange={this.onFiltersChange}
                    />
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-default" onClick={this.onClickClose}>
                        Annuler
                    </button>
                    { addSelectedButton }
                    { addFilterButton }
                </div>
            </Modal>
        );

    },
    
    componentDidMount: function()
    {
        _.bindAll(this, 'onResize');
        $(window).on('resize', this.onResize);
        this.onResize();
    },
    
    componentWillUnmount: function()
    {
        $(window).off('resize', this.onResize);
    },
    
    componentDidUpdate: function()
    {
        this.resizeList();
    },
    
    resizeList: function()
    {
        var $el = $(ReactDOM.findDOMNode(this));
        
        var $header = $el.find('.modal-header');
        var $footer = $el.find('.modal-footer');
        var $children = $el.find('.bubbles-channel > div');
        var $list = $el.find('.list-bubbles');
        var windowHeight = $(window).height();
        var listHeight = windowHeight - $header.outerHeight() - $footer.outerHeight() - 100;
        $children.each(function()
        {
            if(!$(this).is('.list-bubbles') && !$(this).is('.loading-block'))
            {
                listHeight -= $(this).outerHeight();
            }
        })
        
        $list.css({
            'height': listHeight+'px'
        });
    },
    
    onResize: function()
    {
        this.resizeList();
    },
    
    onLoaded: function(bubbles)
    {
        this.resizeList();
    },
    
    onChannelChange: function(channel)
    {
        this.setState({
            channel: channel
        });
    },
    
    onFiltersChange: function(filters)
    {    
        this.resizeList();
        
        this.setState({
            filters: filters
        });
    },
    
    onBubblesChange: function(bubbles)
    {
        this.resizeList();
        
        this.setState({
            selectedBubbles: bubbles
        });
    },
    
    onBubbleClickAdd: function(bubble)
    {
        this.setState({
            selectedBubbles: [bubble]
        }, function()
        {
            this.onCompleteWithBubbles(this.state.selectedBubbles);
        });
    },
    
    onAddSelectedClick: function()
    {
        this.onCompleteWithBubbles(this.state.selectedBubbles);
    },
    
    onAddFilterClick: function()
    {
        var filters = [];
        filters.push({
            name: 'channel_id',
            value: this.state.channel.id
        });
        _.each(this.state.filters, function(filter)
        {
            filters.push(filter);
        });
        
        this.onCompleteWithFilters(filters);
    },
    
    onCompleteWithBubbles: function(bubbles)
    {
        this.props.onComplete({
            bubbles: bubbles
        });
    },
    
    onCompleteWithFilters: function(filters)
    {
        this.props.onComplete({
            filters: filters
        });
    },
    
    onClickClose: function()
    {
        if(this.props.closeModal)
        {
            this.props.closeModal();
        }
        else
        {
            this.close();
        }
    },
    
    onOpen: function()
    {
        this.resizeList();
        
        if(this.props.onOpen)
        {
            this.props.onOpen();
        }
    },
    
    onClose: function()
    {
        this.setState({
            selectedBubbles: [],
            filters: [],
            channel: null
        }, function()
        {
            if(this.props.onClose)
            {
                this.props.onClose();
            }
        });
    }

});

module.exports = BubblesSelectModal;



