var React = require('react-external');
var _ = require('lodash-external');

var Snippets = require('../snippets/index');
var BubbleSnippet = Snippets.Bubble;
var ConditionSnippet = Snippets.Condition;

var ModalActions = Panneau.Actions.Modal;
var ListActions = Panneau.Actions.List;

var PlaylistItem = React.createClass({
    
    propTypes: {
        layout: React.PropTypes.string,
        data: React.PropTypes.object.isRequired,
        onClickRemove: React.PropTypes.func,
        onConditionChange: React.PropTypes.func
    },
    
    getDefaultProps: function() {
        return {
            layout: 'bubble'
        };
    },

    render: function()
    {
        var cols = [];
        cols.push(this.renderColDrag(1));
        cols.push(this.renderColSnippet(6));
        cols.push(this.renderColConditions(4));
        cols.push(this.renderColRemove(1));
        
        return (
            <div className="list-item">
                { cols }
            </div>
        )
    },
    
    renderColDrag: function(size)
    {
        var colSize = typeof(size) !== 'undefined' ? size:1;
        var colClassName = 'list-item-col-drag list-item-col-'+colSize;
        
        return (
            <div key="col_drag" className={colClassName}>
                <span className="glyphicon glyphicon-menu-hamburger"></span>
            </div>
        );
    },
    
    renderColSnippet: function(size)
    {
        var colSize = typeof(size) !== 'undefined' ? size:12;
        var colClassName = 'list-item-col-'+colSize;
        var type = _.get(this.props, 'data.bubble.type', '');
        var data = _.get(this.props, 'data.bubble.snippet');
        
        return (
            <div key="col_snippet" className={colClassName}>
                <BubbleSnippet data={data} />
            </div>
        );
    },
    
    renderColConditions: function(size)
    {
        var colSize = typeof(size) !== 'undefined' ? size:12;
        var colClassName = 'list-item-col-top list-item-col-'+colSize;
        
        var data = _.get(this.props, 'data.condition.snippet');
        var fields = _.get(this.props, 'data.condition.fields', {});
        
        var hasCondition = false;
        _.each(fields, function(v, k)
        {
            var isArray = _.isArray(v);
            if((v && !isArray) || (isArray && v.length))
            {
                hasCondition = true;
            }
        })
        
        var condition;
        if(hasCondition)
        {
            condition = (
                <ConditionSnippet data={data} />
            );
        }
        else
        {
            condition = (
                <p>En tout temps</p>
            );
        }
        
        return (
            <div key="col_conditions" className={colClassName}>
                { condition }
                <div className="">
                    <a href="#" onClick={this.onConditionEditClick}>Modifier</a>
                </div>
            </div>
        );
    },
    
    renderColRemove: function(size)
    {
        var colSize = typeof(size) !== 'undefined' ? size:1;
        var colClassName = 'list-item-col-last list-item-col-'+colSize;
        return (
            <div key="col_remove" className={colClassName}>
                <button type="button" className="btn btn-default btn-no-border glyphicon glyphicon-remove" onClick={this.onClickRemove}></button>
            </div>
        );
    },
    
    onConditionEditClick: function(e)
    {
        e.preventDefault();
        
        var bubble = _.get(this.props, 'data');
        var condition = _.get(this.props, 'data.condition', {});
        
        Panneau.dispatch(ModalActions.openModal('Conditions', {
            bubbles: [bubble],
            condition: condition,
            onComplete: this.onModalComplete,
            onClose: this.onModalClose
        }));
    },
    
    onModalComplete: function(condition)
    {
        var bubble = _.get(this.props, 'data', null);
        _.set(bubble || {}, 'condition', condition);
        
        Panneau.dispatch(ListActions.updateItem(this.props.index, bubble, this.props.list));
        Panneau.dispatch(ModalActions.closeModal('Conditions'));
        
        if(this.props.onConditionChange)
        {
            this.props.onConditionChange(condition);
        }
    },
    
    onModalClose: function()
    {
        
    },
    
    onConditionUpdated: function(condition)
    {
        
    },
    
    onClickRemove: function()
    {
        if(this.props.onClickRemove)
        {
            this.props.onClickRemove(this.props.data);
        }
    }

});

module.exports = PlaylistItem;



