var React = require('react-external');
var ReactDOM = require('react-dom-external');
var $ = require('jquery-external');

var FormComponent = Panneau.Components.Form.Component;

var DangerZone = React.createClass({
    
    propTypes: {
        form: React.PropTypes.object.isRequired,
        delete: React.PropTypes.bool
    },
    
    getDefaultProps: function()
    {
        return {
            delete: true,
            deleteTitle: 'Supprimer',
            deleteDescription: '',
            deleteConfirmation: 'tes-vous certain de vouloir supprimer cet item?',
            form: {
                buttons: [
                    {
                        'className': 'btn btn-danger',
                        'type': 'submit',
                        'label': 'Supprimer'
                    }
                ]
            }
        };
    },
    
    getInitialState: function()
    {
        return {
            opened: false
        };
    },
    
    render: function()
    {
        
        var optionsStyle = {
            display: 'none'
        };
        if(this.state.opened)
        {
            optionsStyle.display = 'block';
        }
        
        var toggleClassName = 'glyphicon glyphicon-menu-down';
        if(this.state.opened)
        {
            toggleClassName = 'glyphicon glyphicon-menu-up';
        }
        
        var formProps = _.omit(this.props.form, ['errors', 'onSubmit']);
        
        var deleteOptions = null;
        if(this.props.delete)
        {
            var deleteTitle = this.props.deleteTitle && this.props.deleteTitle.length ? (
                <h4>{ this.props.deleteTitle }</h4>
            ):null;
            var deleteDescription = this.props.deleteDescription && this.props.deleteDescription.length ? (
                <p>{ this.props.deleteDescription }</p>
            ):null;
            deleteOptions = (
                <div ref="options" className="options" style={optionsStyle}>
                    <div className="option">
                        { deleteTitle }
                        
                        { deleteDescription }
                        
                        <FormComponent {...formProps} onSubmit={this.onSubmitDelete} />
                    </div>
                </div>
            );
        }
        
        return (
            <div className="danger-zone">
                <div className="toggle">
                    <button type="button" onClick={this.onClickToggle}>
                        <span className="glyphicon glyphicon-exclamation-sign"></span>
                        Zone prilleuse <span className={toggleClassName}></span>
                    </button>
                </div>
                
                { deleteOptions }
            </div>
        );
    },
    
    componentDidUpdate: function(prevProps, prevState)
    {
        if(this.state.opened !== prevState.opened)
        {
            var $options = $(ReactDOM.findDOMNode(this.refs.options));
            if(this.state.opened)
            {
                
            }
            else
            {
                
            }
        }
    },
    
    onClickToggle: function(e)
    {
        e.preventDefault();
        
        this.setState({
            opened: !this.state.opened
        });
    },
    
    onSubmitDelete: function(e)
    {
        if(!confirm(this.props.deleteConfirmation))
        {
            e.preventDefault();
        }
        
        if(this.props.onDelete)
        {
            this.props.onDelete(e);
        }
    }
    
});

module.exports = DangerZone;



