var React = require('react-external');

var $ = require('jquery-external');

var FormActions = Panneau.Actions.Form;
var Modal = Panneau.Components.Modals.Modal;
var ChannelActions = require('../../actions/ChannelActions');

var Requests = require('../../requests/index');

var ChannelAddModal = React.createClass({
    
    getDefaultProps: function()
    {
        return {
            visible: false,
            channels: null,
            channelsAdded: []
        };
    },
    
    getInitialState: function()
    {
        return {
            channels: this.props.channels,
            channelsAdded: this.props.channelsAdded
        };
    },

    render: function()
    {
        var channels = null;
        if(this.state.channels)
        {
            channels = this.renderChannels(this.state.channels);
        }
        
        return (
            <Modal closeModal={this.props.closeModal} visible={this.props.visible} onClose={this.onClose}>
                <div className="modal-header">
                    <button type="button" className="close" onClick={this.onClickClose} aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                    <h4 className="modal-title">Ajouter une chane</h4>
                </div>
                <div className="modal-body">
                    
                    <div className="row">

                        <div className="col-sm-3">
                            <ul className="nav nav-pills nav-stacked">
                                <li role="presentation" className="active"><a href="#">Culture</a></li>
                            </ul>    
                        </div>
                        <div className="col-sm-9">
                            
                            <div className="list list-add-channel">
                                <div className="list-items">
                                    <div className="list-item">
                                        <h3>Culture</h3>
                                        
                                        <div className="list list-thumbnails">
                                            { channels }
                                        </div>
                                    </div>
                                </div>
                            </div>
                            
                        </div>
                        
                    </div>
                    
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-default" onClick={this.onClickClose}>Fermer</button>
                </div>
            </Modal>
        );

    },
    
    renderChannels: function(channels)
    {
        return channels.map(_.bind(this.renderChannel, this));
    },
    
    renderChannel: function(channel)
    {
        var added = _.find(this.state.channelsAdded, function(channelAdded)
        {
            return ''+channelAdded.id === ''+channel.id;
        }) ? true:false;
        var btnLabel = added ? 'Enlever':'Ajouter';
        var btnIcon = 'glyphicon '+(added ? 'glyphicon-remove':'glyphicon-plus');
        
        var onClick = _.bind(function()
        {
            if(added)
            {
                this.onRemove(channel);
            }
            else
            {
                this.onAdd(channel);
            }
        }, this);
        
        var key = 'channel_'+channel.id;
        
        var title = _.get(channel, 'snippet.title');
        var description = _.get(channel, 'snippet.description');
        var picture = _.get(channel, 'snippet.picture.link');
        var imageStyle = {
            backgroundImage: picture ? ('url('+picture+')'):'none'
        };
        
        var descriptionParagraph = null;
        if(description && description.length)
        {
            descriptionParagraph = (
                <p>{ description }</p>
            );
        }
        
        return (
            <div key={key} className="list-item list-item-col-4">
                <div className="thumbnail">
                    <div className="image" style={imageStyle}></div>
                </div>
                <div className="detail">
                    <h4>{ title }</h4>
                    { descriptionParagraph }
                    
                    <button className="btn btn-default btn-sm" onClick={onClick}>
                        <span className={btnIcon}></span> { btnLabel }
                    </button>
                </div>
            </div>
        );
    },
    
    componentWillMount: function()
    {
        if(!this.state.channels)
        {
            Requests.Channels.load()
                .then(_.bind(this.onChannelsLoaded, this));
        }
    },
    
    onChannelsLoaded: function(channels)
    {
        this.setState({
            channels: channels,
            channel: channels && channels.length ? channels[0]:null
        });
    },
    
    onClickClose: function()
    {
        if(this.props.closeModal)
        {
            this.props.closeModal();
        }
        else
        {
            this.close();
        }
    },
    
    onAdd: function(channel)
    {
        var currentChannels = Immutable.fromJS(this.state.channelsAdded);
        var newChannels = currentChannels.push(channel);
        
        if(currentChannels !== newChannels)
        {
            this.setState(
            {
                channelsAdded: newChannels.toJS()
            });
        }
        
        if(this.props.onAdd)
        {
            this.props.onAdd(channel);
        }
    },
    
    onRemove: function(channel)
    {
        var index = _.findIndex(this.state.channelsAdded, function(channelAdded)
        {
            return channelAdded.id+'' === channel.id+'';
        });
        if(index >= 0)
        {
            var currentChannels = Immutable.fromJS(this.state.channelsAdded);
            var newChannels = currentChannels.delete(index);
            
            if(currentChannels !== newChannels)
            {
                this.setState(
                {
                    channelsAdded: newChannels.toJS()
                });
            }
        }
        
        
        if(this.props.onRemove)
        {
            this.props.onRemove(channel);
        }
    },
    
    onClose: function()
    {
        if(this.props.onClose)
        {
            this.props.onClose();
        }
    }

});

module.exports = ChannelAddModal;



