var graphql = require('./graphql');
var _ = require('lodash-external');

var Fragments = require('./Fragments');

module.exports = {
    
    load: function(params)
    {
        return graphql(`
            query Query<queryArgs>{
                data: channels<objectArgs>
                {
                    ...channelFields
                }
            }
        `, params, {
            fragments: [Fragments.channelFields]
        }).then(function(result)
        {
            return result.data;
        });
    },
    
    loadForScreen: function(screen)
    {
        var screen_id = _.isObject(screen) ? _.get(screen, 'id'):screen;
        return this.load({
            screen_id: screen_id
        });
    }
    
};



