var React = require('react-external');
var ReactDOM = require('react-dom-external');

var TimeField = Panneau.Components.Fields.Time;

var Timeline = React.createClass({
    
    propTypes: {
        screen_id: React.PropTypes.number,
        items: React.PropTypes.object
    },
    
    getDefaultProps: function()
    {
        return {
            items: {}
        };
    },
    
    getInitialState: function()
    {
        var currentTime = (new Date()).getTime();
        return {
            time: currentTime,
            currentTime: currentTime,
            filter: 'now',
            cyclesCount: 2,
            open: false
        };
    },
    
    render: function()
    {    
        var timeline = this.renderTimeline();
        
        var pickerStyle = {};
        if(this.state.filter !== 'time')
        {
            pickerStyle.display = 'none';
        }
        
        var date = new Date(this.state.time);
        var hours = date.getHours();
        var minutes = date.getMinutes();
        
        var time = [];
        time.push(hours < 10 ? ('0'+hours):hours);
        time.push(minutes < 10 ? ('0'+minutes):minutes);
        
        var timeValue = time.join(':');
        
        var toggleIconClassName = 'glyphicon glyphicon glyphicon-menu-'+(this.state.open ? 'down':'up');
        var timelineClassName = 'container-timeline timeline-slider';
        if(this.state.open)
        {
            timelineClassName += ' timeline-open';
        }
        
        return (
            <div className={timelineClassName}>
                        
                <div className="timeline-header">
                    <button type="button" className="btn btn-toggle" onClick={this.onToggleClick}>
                        <span className={toggleIconClassName}></span>
                    </button>
                    
                    <label className="timeline-label" htmlFor="" onClick={this.onToggleClick}>Aperu du diaporama</label>
                    <select className="form-control timeline-selector" name="role" value={this.state.filter} onChange={this.onFilterChange}>
                        <option value="now">maintenant</option>
                        <option value="time"> une certaine heure</option>
                    </select>
                    <div className="timeline-picker" style={pickerStyle}>
                        <TimeField value={timeValue} onChange={this.onTimeChange} />
                    </div>
                </div>
                
                <ul className="list-group list-group-timeline">
                    { timeline }
                </ul>
                
            </div>
        );
    },
    
    renderTimeline: function()
    {
        var cycles = _.get(this.props, 'items.cycles', []);
        if(!cycles || !cycles.length)
        {
            return (
                <li className="list-group-item list-group-item-empty">
                    <div className="middle">Aucun contenu pour le moment.</div>
                </li>
            );
        }
        
        var items = [];
        
        var currentTime = this.state.time;
        var currentCycle, cycleDuration;
        var index = 0;
        for(var i = 0; i < this.state.cyclesCount; i++)
        {
            if(i > 0)
            {
                items.push(this.renderSeparator(index));
            }
            
            currentCycle = this.getCurrentCycle(currentTime);
            cycleDuration = this.getCycleDuration(currentCycle);
            
            var cycleStartTime = (currentCycle.start*1000);
            var delta = currentTime - cycleStartTime;
            var position = delta % cycleDuration;
            var cyclesCount = Math.floor(delta / cycleDuration);
            var currentItem = this.getItemAtPosition(currentCycle, position);
            
            var itemCurrentTime = cycleStartTime + (cyclesCount * cycleDuration);
            for(var ii = 0, il = currentCycle.items.length; ii < il; ii++)
            {
                var it = currentCycle.items[ii];
                items.push(this.renderItem(it, itemCurrentTime, it.id === currentItem.id, index));
                itemCurrentTime += (it.duration * 1000);
                index++;
            }
            
            currentTime += cycleDuration;
        }
        
        return items;
    },
    
    renderItem: function(it, time, current, index)
    {
        var endTime = time + (it.duration*1000);
        var date = new Date(time);
        var hours = date.getHours();
        var minutes = date.getMinutes();
        var seconds = date.getSeconds();
        var timeLabel = [];
        timeLabel.push(hours < 10 ? ('0'+hours):hours);
        timeLabel.push(minutes < 10 ? ('0'+minutes):minutes);
        timeLabel.push(seconds < 10 ? ('0'+seconds):seconds);
        timeLabel = timeLabel.join(':')
        
        var key = 'item_'+index;
        var bubbles = _.get(this.props, 'items.bubbles', []);
        var bubble = _.find(bubbles, function(bubble)
        {
            return bubble.id+'' === it.bubble_id+'';
        })
        var pictureLink = _.get(bubble || {}, 'snippet.picture.link');
        var thumbnailStyle = {
            'backgroundImage': 'url('+pictureLink+')',
            'backgroundSize': 'cover'
        };
        
        var itemClassName = 'list-group-item';
        if(this.state.currentTime >= time && this.state.currentTime < endTime)
        {
            itemClassName += ' list-group-item-current';
            timeLabel = 'Maintenant';
        }
        
        return (
            <li key={key} className={itemClassName}>
                <div className="timeline-thumbnail" style={thumbnailStyle}></div>
                <span>{ timeLabel }</span>
            </li>
        );
    },
    
    renderSeparator: function(index)
    {
        var key = 'sep_'+index;
        return (
            <li key={key} className="list-group-item list-group-item-separator">
                <div className="border-timeline">
                    <div className="list-group-item-separator-border"></div>
                    <div className="glyphicon glyphicon-retweet list-group-item-separator-icon"></div>
                    <div className="list-group-item-separator-border"></div>
                </div>
            </li>
        );
    },
    
    componentDidMount: function()
    {
        _.bindAll(this, 'onClockInterval', 'onResize');
        this.clockInterval = setInterval(this.onClockInterval, 500);
        
        $(window).on('resize', this.onResize);
        
        this.onResize();
    },
    
    componentWillUnmount: function()
    {
        $(window).off('resize', this.onResize);
        
        if(this.clockInterval)
        {
            clearInterval(this.clockInterval);
        }
    },
    
    onResize: function()
    {
        var $el = $(ReactDOM.findDOMNode(this));
        var width = $el.outerWidth();
        var cycleWidth = 0;
        var $items = $el.find('.list-group-item');
        for(var i = 0, il = $items.length; i < il; i++)
        {
            cycleWidth += $items.eq(i).outerWidth();
            if($items.eq(i).is('.list-group-item-separator'))
            {
                break;
            }
        }
        
        var cyclesCount = Math.max(Math.ceil(width/cycleWidth), 1) + 1;
        this.setState({
            cyclesCount: cyclesCount
        });
    },
    
    onToggleClick: function()
    {
        this.setState({
            open: !this.state.open
        });
    },
    
    onFilterChange: function(e)
    {
        this.setState({
            filter: e.target.value,
            time: e.target.value === 'now' ? this.state.currentTime:this.state.time,
            open: true
        });
    },
    
    onTimeChange: function(value)
    {
        var timeParts = value.split(':');
        var date = new Date();
        date.setHours(parseInt(timeParts[0]));
        date.setMinutes(parseInt(timeParts[1]));
        date.setSeconds(0);
        
        this.setState({
            time: date.getTime()
        });
    },
    
    onClockInterval: function()
    {
        var newState = {
            currentTime: (new Date()).getTime()
        };
        
        if(this.state.filter === 'now')
        {
            newState.time = newState.currentTime;
        }
        
        this.setState(newState);
    },
    
    getCurrentCycle: function(time)
    {
        //Find current cycle
        var currentCycle = null;
        var cycles = _.get(this.props, 'items.cycles');
        for(var i = 0, cl = cycles.length; i < cl; i++)
        {
            var cycle = cycles[i];
            var startTime = (new Date(cycle.start * 1000)).getTime();
            var endTime = (new Date(cycle.end * 1000)).getTime();
            
            if(time >= startTime && time < endTime)
            {
                currentCycle = cycle;
                break;
            }
        }
        
        return currentCycle;
    },
    
    getCycleDuration: function(cycle)
    {
        var duration = 0;
        for(var i = 0, il = cycle.items.length; i < il; i++)
        {
            var it = cycle.items[i];
            duration += (it.duration * 1000);
        }
        
        return duration;
    },
    
    getItemAtPosition: function(cycle, position)
    {
        //Find the item covering this position
        var currentPosition = 0;
        var currentItem = cycle.items[0];
        for(var i = 0, il = cycle.items.length; i < il; i++)
        {
            var it = cycle.items[i];
            currentPosition += it.duration;
            if(currentPosition > position)
            {
                break;
            }
            currentItem = it;
        }
        
        return currentItem;
    }
    
});

module.exports = Timeline;



