var _routes = {};

module.exports = {
    
    route: function(key, opts)
    {
        if(_.isObject(key))
        {
            var routes = key;
            for(var key in routes)
            {
                _routes[key] = routes[key];
            }
            return;
        }
        
        var prefix = 'http://'+window.location.host;
        var url = (_routes[key] || key).replace(prefix,'');
        
        if(typeof(opts) !== 'undefined')
        {
            for(var k in opts)
            {
                url = url.replace(':'+k,opts[k]);
            }
        }
        
        return url;
    }
    
};



