var React = require('react-external');
var _ = require('lodash-external');
var Panneau = require('panneau');

var SubFieldMixin = require('../../mixins/SubField.react');

var ScreenResolutionField = require('./ScreenResolution.react');

var ScreenTechnicalField = React.createClass({
    
    mixins: [SubFieldMixin],
    
    propTypes: {
        name: React.PropTypes.string,
        value: React.PropTypes.object,
        onChange: React.PropTypes.func
    },
    
    getDefaultProps: function()
    {
        return {
            
        };
    },
    
    render: function()
    {
        
        var fieldScreenResolution = this.renderSubField(ScreenResolutionField, 'resolution', {
            
        });
        
        var fieldVendor = this.renderTextField('vendor', {
            wrapperClassName: 'col-xs-12 col-sm-4',
            label: 'Vendeur'
        });
        
        var fieldModel = this.renderTextField('model', {
            wrapperClassName: 'col-xs-12 col-sm-4',
            label: 'Modle'
        });
        
        var fieldSerialNumber = this.renderTextField('serial_number', {
            wrapperClassName: 'col-xs-12 col-sm-4',
            label: 'Numro de srie'
        });
        
        var fieldSize = this.renderTextField('size', {
            label: 'Taille'
        });
        
        return (
            <div className="form-group form-group-screen-technical">
                <div className="row">
                    { fieldVendor }
                    { fieldModel }
                    { fieldSerialNumber }
                </div>
                
                <div className="row">
                    <div className="col-sm-4">
                        { fieldSize }
                    </div>
                    <div className="col-sm-8">
                        { fieldScreenResolution }
                    </div>
                </div>
            </div>
        );
    }
    
    
});

module.exports = ScreenTechnicalField;



