var graphql = require('./graphql');
var _ = require('lodash-external');

var Fragments = require('./Fragments');

module.exports = {

    saveCondition: function(condition, id)
    {
        var params = _.extend(id ? {
            condition_id: id
        }:{}, condition);
        
        return graphql(`
                mutation Mutation<queryArgs>{
                    saveCondition<objectArgs>
                    {
                        ...conditionFields
                    }
                }
            `, params, {
                fragments: [Fragments.conditionFields]
            }).then(function(result)
            {
                return result.saveCondition;
            });
    },

    addBubblesWithCondition: function(playlist, bubbles, condition)
    {
        var playlist_id = _.isObject(playlist) ? _.get(playlist, 'id'):playlist;
        var condition_id = _.isObject(condition) ? _.get(condition, 'id'):condition;
        
        var bubblesIds = _.map(bubbles, function(bubble)
        {
            return bubble.id+'';
        });
        
        return graphql(`
                mutation Mutation<queryArgs>{
                    data: playlistAddBubbles<objectArgs>
                    {
                        ...playlistItemFields
                    }
                }
            `, {
                playlist_id: playlist_id+'',
                bubble_ids: bubblesIds,
                condition_id: condition_id+''
            }, {
                argsTypes: {
                    playlist_id: 'String',
                    bubble_ids: '[String]',
                    condition_id: 'String'
                },
                fragments: [
                    Fragments.playlistItemFields,
                    Fragments.bubbleFields,
                    Fragments.conditionFields
                ]
            }).then(function(result)
            {
                return result.data;
            });
    },

    addFiltersWithCondition: function(playlist, filters, condition)
    {
        var playlist_id = _.isObject(playlist) ? _.get(playlist, 'id'):playlist;
        var condition_id = _.isObject(condition) ? _.get(condition, 'id'):condition;
        
        return graphql(`
                mutation Mutation<queryArgs>{
                    data: playlistAddFilters<objectArgs>
                    {
                        ...playlistItemFields
                    }
                }
            `, {
                playlist_id: playlist_id+'',
                filters: JSON.stringify(filters),
                condition_id: condition_id+''
            }, {
                argsTypes: {
                    playlist_id: 'String',
                    filters: 'String',
                    condition_id: 'String'
                },
                fragments: [
                    Fragments.playlistItemFields,
                    Fragments.bubbleFields,
                    Fragments.conditionFields
                ]
            }).then(function(result)
            {
                return result.data;
            });
    },

    addBubbles: function(playlist, bubbles)
    {
        var playlist_id = _.isObject(playlist) ? _.get(playlist, 'id'):playlist;
        
        var bubblesIds = _.map(bubbles, function(bubble)
        {
            return bubble.id+'';
        });
        
        return graphql(`
                mutation Mutation<queryArgs>{
                    data: playlistAddBubbles<objectArgs>
                    {
                        ...playlistItemFields
                    }
                }
            `, {
                playlist_id: playlist_id+'',
                bubble_ids: bubblesIds
            }, {
                argsTypes: {
                    playlist_id: 'String',
                    bubble_ids: '[String]'
                },
                fragments: [
                    Fragments.playlistItemFields,
                    Fragments.bubbleFields,
                    Fragments.conditionFields
                ]
            }).then(function(result)
            {
                return result.data;
            });
    },

    addFilters: function(playlist, filters)
    {
        var playlist_id = _.isObject(playlist) ? _.get(playlist, 'id'):playlist;
        
        return graphql(`
                mutation Mutation<queryArgs>{
                    data: playlistAddFilters<objectArgs>
                    {
                        ...playlistItemFields
                    }
                }
            `, {
                playlist_id: playlist_id+'',
                filters: JSON.stringify(filters)
            }, {
                argsTypes: {
                    playlist_id: 'String',
                    filters: 'String',
                    condition_id: 'String'
                },
                fragments: [
                    Fragments.playlistItemFields,
                    Fragments.bubbleFields,
                    Fragments.conditionFields
                ]
            }).then(function(result)
            {
                return result.data;
            });
    },

    removeItem: function(playlist, it)
    {
        var playlist_id = _.isObject(playlist) ? _.get(playlist, 'id'):playlist;
        var item_id = _.isObject(it) ? _.get(it, 'id'):it;
        
        return graphql(`
                mutation Mutation<queryArgs>{
                    data: playlistRemoveBubble<objectArgs>
                    {
                        ...playlistItemFields
                    }
                }
            `, {
                playlist_id: playlist_id+'',
                item_id: item_id
            }, {
                argsTypes: {
                    playlist_id: 'String',
                    item_id: 'String'
                },
                fragments: [
                    Fragments.playlistItemFields,
                    Fragments.bubbleFields,
                    Fragments.conditionFields
                ]
            }).then(function(result)
            {
                return result.data;
            });
    },

    updateOrder: function(playlist, ids)
    {
        var playlist_id = _.isObject(playlist) ? _.get(playlist, 'id'):playlist;
        
        return graphql(`
                mutation Mutation<queryArgs>{
                    data: playlistUpdateOrder<objectArgs>
                    {
                        ...playlistItemFields
                    }
                }
            `, {
                playlist_id: playlist_id+'',
                ids: ids
            }, {
                argsTypes: {
                    playlist_id: 'String',
                    ids: '[Int]'
                },
                fragments: [
                    Fragments.playlistItemFields,
                    Fragments.bubbleFields,
                    Fragments.conditionFields
                ]
            }).then(function(result)
            {
                return result.data;
            });
    }
};



