
var Promise = Promise || require('promise');
var agent = require('superagent-promise-external')(require('superagent-external'), Promise);
var $ = require('jquery-external');

function buildArgs(params, types)
{
    var queryArgs = [];
    var objectArgs = [];
    _.each(params, function(v, k)
    {
        var type = _.get(types, k, _.isArray(v) ? '[String]':'String');
        queryArgs.push('$'+k+': '+type);
        objectArgs.push(k+': $'+k);
    });
    queryArgs = queryArgs.length ? ('('+queryArgs.join(',')+')'):'';
    objectArgs = objectArgs.length ? ('('+objectArgs.join(',')+')'):'';
            
    return {
        object: objectArgs,
        query: queryArgs
    };
}

module.exports = function(query, params, opts)
{
    opts = _.extend({
        argsTypes: {},
        fragments: []
    }, opts);
    
    if(typeof(params) === 'undefined')
    {
        params = null;
    }
    
    if(query.match(/\<(object|query)Args\>/))
    {
        var args = buildArgs(params, opts.argsTypes);
        query = query.replace(/\<objectArgs\>/g, args.object)
                    .replace(/\<queryArgs\>/g, args.query);
    }
    
    _.each(opts.fragments, function(fragment, key)
    {
        query += fragment;
    });
    
    return agent
        .post('/graphql/query')
        .send({
            query: query,
            params: params
        })
        .set('X-CSRF-TOKEN', $('meta[name="csrf-token"]').attr('content'))
        .set('Accept', 'application/json')
        .end()
        .then(function(response)
        {
            if (response.body.errors || !response.body.data)
            {
                return Promise.reject(response.body.errors);
            }
            
            return response.body.data;
        });
};



