var React = require('react-external');
var _ = require('lodash-external');
var Immutable = require('immutable-external');
var request = require('superagent-external');

var URL = require('../../lib/url');

var FormModal = require('./FormModal');

var BubbleFormModal = React.createClass({

    getDefaultProps: function()
    {
        return {
            bubbleType: 'banq_service'
        };
    },

    render: function()
    {
        var props = _.omit(this.props, ['bubbleType', 'title', 'formName', 'formMethod']);
        var id = _.get(this.props, 'formData.id');
        var bubbleType = _.get(this.props, 'formData.type', this.props.bubbleType).replace(/[^a-z0-9\.]+/gi,'');
        var title = id ? 'Modifier un contenu':'Ajouter un contenu';
        var formName = 'bubble.'+bubbleType;
        var formAction = id ? URL.route('organisation.bubbles.update', id):URL.route('organisation.bubbles.store');
        var formMethod = id ? 'put':'post';
        
        return (
            <FormModal
                {...props}
                title={title}
                formName={formName}
                formMethod={formMethod}
                formAction={formAction}
                />
        );

    }

});

module.exports = BubbleFormModal;



