var API_KEY = typeof(GOOGLE_API_KEY) !== 'undefined' ? GOOGLE_API_KEY:'AIzaSyDYuKJ4N-IxhsZHSaiBFwzTE4KTJrE17Js';

function loadGMap(apiKey)
{
    if(typeof(apiKey) === 'undefined' || !apiKey)
    {
        apiKey = API_KEY;
    }
    
    return new Promise(function(resolve, reject)
    {
        if(typeof(google) !== 'undefined' && typeof(google.maps) !== 'undefined')
        {
            return resolve();
        }
        
        var callbackName = 'gmapLoaded_'+(new Date()).getTime();
        window[callbackName] = function()
        {
            return resolve();
            window[callbackName] = null;
        };
        var url = 'https://maps.googleapis.com/maps/api/js?key='+apiKey+'&callback='+callbackName;
        var script = document.createElement('script');
        script.src = url;
        script.type = 'text/javascript';
        document.getElementsByTagName('head')[0].appendChild(script);
    });
}

function geocode(address)
{
    return new Promise(function(resolve, reject)
    {
        var geocoder = new google.maps.Geocoder();
        
        geocoder.geocode({
            'address': address
        }, function(results, status)
        {
            if (status == google.maps.GeocoderStatus.OK)
            {
                resolve(results[0].geometry.location);
            }
            else
            {
                reject(status);
            }
        });
    });
}

module.exports = {
    API_KEY: API_KEY,
    loadGMap: loadGMap,
    geocode: geocode
};



