var React = require('react-external');
var _ = require('lodash-external');
var Panneau = require('panneau');

var SubFieldMixin = require('../../mixins/SubField.react');

var ScreenResolutionField = React.createClass({
    
    mixins: [SubFieldMixin],
    
    propTypes: {
        name: React.PropTypes.string,
        value: React.PropTypes.object,
        onChange: React.PropTypes.func
    },
    
    getDefaultProps: function()
    {
        return {
            
        };
    },
    
    render: function()
    {
        
        var inputResolutionX = this.renderTextField('x', {
            wrapperClassName: 'col-xs-6 col-md-6',
            prefix: 'x:',
            suffix: 'px'
        });
        var inputResolutionY = this.renderTextField('y', {
            wrapperClassName: 'col-xs-6 col-md-6',
            prefix: 'y:',
            suffix: 'px'
        });
        
        return (
            <div className="form-group form-group-screen-resolution">
                <label className="control-label">Rsolution</label>
                <div className="row">
                    { inputResolutionX }
                    { inputResolutionY }
                </div>
            </div>
        );
    }
    
    
});

module.exports = ScreenResolutionField;



