var graphql = require('./graphql');
var _ = require('lodash-external');

var Fragments = require('./Fragments');

module.exports = {
    
    linkScreen: function(auth_code)
    {
        return graphql(`
                mutation Mutation<queryArgs>{
                    data: organisationLinkScreen<objectArgs>
                    {
                        ...screenFields
                    }
                }
            `, {
                auth_code: auth_code
            }, {
                fragments: [Fragments.screenFields]
            }).then(function(result)
            {
                return result.data;
            });
    },
    
    inviteUser: function(email, role_id)
    {
        return graphql(`
                mutation Mutation<queryArgs>{
                    data: organisationInviteUser<objectArgs>
                    {
                        ...invitationFields
                    }
                }
            `, {
                email: email,
                role_id: role_id
            }, {
                fragments: [Fragments.invitationFields]
            }).then(function(result)
            {
                return result.data;
            });
    },
    
    updateUser: function(user, role_id)
    {
        var user_id = _.isObject(user) ? _.get(user, 'id'):user;
        return graphql(`
                mutation Mutation<queryArgs>{
                    data: organisationUpdateUser<objectArgs>
                    {
                        ...userFields
                    }
                }
            `, {
                user_id: user_id,
                role_id: role_id
            }, {
                fragments: [Fragments.userFields]
            }).then(function(result)
            {
                return result.data;
            });
    },
    
    removeUser: function(user)
    {
        var user_id = _.isObject(user) ? _.get(user, 'id'):user;
        return graphql(`
                mutation Mutation<queryArgs>{
                    data: organisationRemoveUser<objectArgs>
                    {
                        ...userFields
                    }
                }
            `, {
                user_id: user_id
            }, {
                fragments: [Fragments.userFields]
            }).then(function(result)
            {
                return result.data;
            });
    },
    
    updateInvitation: function(invitation, role_id)
    {
        var invitation_id = _.isObject(invitation) ? _.get(invitation, 'id'):invitation;
        return graphql(`
                mutation Mutation<queryArgs>{
                    data: organisationUpdateInvitation<objectArgs>
                    {
                        ...invitationFields
                    }
                }
            `, {
                invitation_id: invitation_id,
                role_id: role_id
            }, {
                fragments: [Fragments.invitationFields]
            }).then(function(result)
            {
                return result.data;
            });
    },
    
    removeInvitation: function(invitation)
    {
        var invitation_id = _.isObject(invitation) ? _.get(invitation, 'id'):invitation;
        return graphql(`
                mutation Mutation<queryArgs>{
                    data: organisationRemoveInvitation<objectArgs>
                    {
                        ...invitationFields
                    }
                }
            `, {
                invitation_id: invitation_id
            }, {
                fragments: [Fragments.invitationFields]
            }).then(function(result)
            {
                return result.data;
            });
    }
    
};



