var React = require('react-external');
var Panneau = require('panneau');

var OrganisationsField = React.createClass({

    propTypes: {
        name: React.PropTypes.string
    },
    
    getDefaultProps: function()
    {
        return {
            
        };
    },

    render: function()
    {
        var label;
        if(this.props.label)
        {
            label = <label htmlFor={this.props.name} className="control-label">{this.props.label}</label>;
        }
        
        var organisations = (this.props.value || []).map(_.bind(this.renderOrganisation, this));
        if(!organisations.length)
        {
            organisations = (
                <div className="list-item list-item-empty">
                    Aucune organisation pour le moment.
                </div>
            );
        }
        
        return (
            <div className="form-group form-group-organisations">
                { label }
                <div className="list list-rows list-organisations">
                    { organisations }
                </div>
            </div>
        );
    },
    
    renderOrganisation: function(organisation, index)
    {
        var onClickRemove = _.bind(function(e)
        {
            this.onClickRemove(e, organisation, index);
        }, this);
        
        var role = _.get(organisation, 'pivot.role.name');
        var name = this.props.name+'[]';
        var key = 'organisation_'+index;
        
        return (
            <div key={key} className="list-item">
                <div className="list-item-col list-item-col-icon">
                    <span className="glyphicon glyphicon-lock"></span>
                </div>
                <div className="list-item-col">
                    { organisation.name }
                    <input type="hidden" name={name} value={organisation.id} />
                </div>
                <div className="list-item-col list-item-col-role">
                    { role }
                </div>
                <div className="list-item-col list-item-col-actions list-item-col-last">
                    <button type="button" className="btn btn-default" onClick={onClickRemove}>
                        <span className="glyphicon glyphicon-remove"></span>
                    </button>
                </div>
            </div>
        );
    },
    
    onClickRemove: function(e, organisation, index)
    {
        e.preventDefault();
        
        if(!confirm('tes-vous certain de vouloir vous enlever de cet organisation?'))
        {
            return;
        }
        
        var currentValue = Immutable.fromJS(this.props.value || []);
        var newValue = currentValue.delete(index);
        
        if(currentValue !== newValue)
        {
            var value = newValue.toJS();
            
            if(this.props.onChange)
            {
                this.props.onChange(value);
            }
        }
    }

});

module.exports = OrganisationsField;



