
var FormActions = Panneau.Actions.Form;
var ModalActions = Panneau.Actions.Modal;
var ResourceActions = Panneau.Actions.Resource;

var request = require('superagent-external');
var $ = require('jquery-external');

module.exports = {
    
    linkScreen: function(authCode, form)
    {
        return function(dispatch)
        {
            request.post(URL.route('organisation.screens.link'))
                .send({
                    'auth_code': authCode
                })
                .set('X-CSRF-TOKEN', $('meta[name="csrf-token"]').attr('content'))
                .set('Accept', 'application/json')
                .end(function(err, response)
                {
                    if(err)
                    {
                        
                        if(response && response.body)
                        {
                            dispatch(FormActions.updateErrors(response.body, form || 'organisation.screens.link'));
                        }
                        else
                        {
                            dispatch(FormActions.addError('auth_code', 'error', form || 'organisation.screens.link'));
                        }
                        
                        return;
                    }
                    
                    var screen = response.body;
                    
                    dispatch(ResourceActions.updateResource('screens', screen.id, screen));
                    //dispatch(ResourceActions.updateResource('organisations', organisation.id, 'screen_ids', 1));
                    dispatch(ModalActions.closeModal());
                    
                    return;
                });
        };
    }
    
};



