var React = require('react-external');
var _ = require('lodash-external');
var Immutable = require('immutable-external');

var WeekdaysField = require('../fields/Weekdays.react');

var ConditionWeekdaysField = React.createClass({

    propTypes: {
        name: React.PropTypes.string
    },
    
    getDefaultProps: function()
    {
        return {
            name: 'condition_weekdays',
            value: null,
            onChange: null
        };
    },

    render: function()
    {
        var value = _.get(this.props, 'value', null);
        var checked = value ? true:false;
        
        var field = null;
        if(value)
        {
            field = (
                <WeekdaysField name={this.props.name} value={value} onChange={this.onChange} />
            );
        }
        
        return (
            <div className="form-group form-group-condition form-group-condition-weekdays">
                
                <div className="form-group form-group-checkbox">
                    <div className="checkbox">
                        <label>
                            <input type="checkbox" autoComplete="off" checked={checked} onChange={this.onCheckChange}/>
                            <span>Afficher seulement certains jours de la semaine</span>
                        </label>
                    </div>
                </div>
                
                { field }
            </div>
        );
    },
    
    onCheckChange: function(e)
    {
        if(e.target.checked)
        {
            this.onChange([]);
        }
        else
        {
            this.onChange(null);
        }
    },
    
    onChange: function(value)
    {
        if(this.props.onChange)
        {
            this.props.onChange(value);
        }
    }

});

module.exports = ConditionWeekdaysField;



