var React = require('react-external');
var _ = require('lodash-external');
var $ = require('jquery-external');
var Immutable = require('immutable-external');

var Requests = require('../requests/index');

var BubblesChannelList = require('./lists/BubblesChannel.react');
var Form = Panneau.Components.Form.Component;
var Paginator = Panneau.Components.Paginator;

var BubblesSelector = React.createClass({
    
    propTypes: {
        channelsParams: React.PropTypes.object,
        channels: React.PropTypes.array,
        bubbles: React.PropTypes.array,
        checkedBubbles: React.PropTypes.array,
        onChange: React.PropTypes.func,
        onChannelChange: React.PropTypes.func,
        onFiltersChange: React.PropTypes.func,
        onClickAdd: React.PropTypes.func,
        onChannelsLoaded: React.PropTypes.func,
        onBubblesLoaded: React.PropTypes.func
    },
    
    getDefaultProps: function()
    {
        return {
            channelsParams: null,
            channels: null,
            bubbles: null,
            checkedBubbles: null
        };
    },
    
    getInitialState: function()
    {
        return {
            channels: this.props.channels,
            channel: this.props.channels && this.props.channels.length ? this.props.channels[0]:null,
            
            loadingChannels: false
        };
    },

    render: function()
    {
        if(!this.state.channels || !this.state.channel)
        {
            var loading = this.renderLoading();
            return (
                <div className="bubbles-selector">
                    { loading }
                </div>
            );
        }
        
        //Channels menu
        var channelsMenu = null;
        if(this.state.channels)
        {
            channelsMenu = this.renderChannels(this.state.channels);
        }
        
        //Bubbles list
        var bubblesList = (
            <BubblesChannelList 
                layout="add" 
                paginatorBottom={false}
                channel={this.state.channel}
                checkedBubbles={this.props.checkedBubbles}
                onLoaded={this.onBubblesLoaded}
                onChange={this.onBubblesChange}
                onClickAdd={this.onBubblesClickAdd}
                onFiltersChange={this.onBubblesFiltersChange}
            />
        );
        
        var channelLoading = null;
        if(this.state.loadingChannels || !this.state.channels)
        {
            channelLoading = this.renderLoading();
        }

        return (
            <div className="bubbles-selector">
                <div className="row">
                    <div className="col-sm-3 col-channels">
                        { channelLoading }
                        { channelsMenu }
                    </div>
                    <div className="col-sm-9 col-bubbles">
                        { bubblesList }
                    </div>
                </div>
            </div>
        );

    },
    
    renderLoading: function()
    {
        return (
            <div className="loading-block">
                <div className="table">
                    <div className="middle">Chargement...</div>
                </div>
                <div className="block"></div>
            </div>
        );
    },
    
    renderChannels: function(channels)
    {
        return (
            <ul className="nav nav-pills nav-stacked">
                { channels.map(_.bind(this.renderChannel, this)) }
            </ul>
        );
    },
    
    renderChannel: function(channel)
    {
        var onClick = _.bind(function() {
            this.onChannelClick(channel);
        }, this);
        
        var key = 'channel_'+channel.id;
        
        var className = '';
        if(this.state.channel && this.state.channel.id === channel.id)
        {
            className += ' active';
        }
        
        var title = _.get(channel, 'snippet.title');
        
        return (
            <li key={key} role="presentation" className={ className }>
                <a href="#" onClick={ onClick }>{ title }</a>
            </li>
        );
    },
    
    componentWillReceiveProps: function(nextProps)
    {
        if(nextProps.checkedBubbles && this.state.checkedBubbles !== nextProps.checkedBubbles)
        {
            this.setState({
                checkedBubbles: nextProps.checkedBubbles
            });
        }
    },
    
    componentDidMount: function()
    {
        _.bindAll(this, 'onChannelsLoaded', 'onBubblesLoaded');
    
        if(!this.state.channels)
        {
            this.loadChannels();
        }
    },
    
    componentDidUpdate: function(prevProps, prevState)
    {
        var channelId = _.get(this.state, 'channel.id', null)+'';
        var prevChannelId = _.get(prevState, 'channel.id', null)+'';
        if(channelId !== prevChannelId && this.props.onChannelChange)
        {
            this.props.onChannelChange(this.state.channel);
        }
    },
    
    loadChannels: function()
    {
        this.setState({
            channels: null,
            loadingChannels: true
        }, function()
        {
            Requests.Channels.load(this.props.channelsParams)
                .then(this.onChannelsLoaded);
        });
    },
    
    onChannelsLoaded: function(channels)
    {
        this.setState({
            channels: channels,
            channel: channels && channels.length ? channels[0]:null,
            loadingChannels: false
        });
        
        if(this.props.onChannelsLoaded)
        {
            this.props.onChannelsLoaded(channels);
        }
    },
    
    onChannelClick: function(channel)
    {
        this.setState({
            channel: channel,
            bubbles: null
        });
    },
    
    onBubblesClickAdd: function(bubble)
    {
        if(this.props.onClickAdd)
        {
            this.props.onClickAdd(bubble);
        }
    },
    
    onBubblesLoaded: function(bubbles)
    {
        if(this.props.onBubblesLoaded)
        {
            this.props.onBubblesLoaded(bubbles);
        }
    },
    
    onBubblesChange: function(bubbles)
    {
        if(this.props.onChange)
        {
            this.props.onChange(bubbles);
        }
    },
    
    onBubblesFiltersChange: function(filters)
    {
        if(this.props.onFiltersChange)
        {
            this.props.onFiltersChange(filters);
        }
    }

});

module.exports = BubblesSelector;



