var React = require('react-external');
var Panneau = require('panneau');
var $ = require('jquery-external');
var _ = require('lodash-external');
var Immutable = require('immutable-external');
var request = require('superagent-external');

var Modal = Panneau.Components.Modals.Modal;
var FormActions = Panneau.Actions.Form.Component;

var FormModal = React.createClass({
    
    contextTypes: {
        store: React.PropTypes.object
    },
    
    getDefaultProps: function()
    {
        return {
            visible: false,
            element: null,
            offsetX: 0,
            offsetY: 0,
            title: '',
            formName: null,
            formFields: [],
            formAction: '',
            formMethod: 'post',
            formData: null,
            formErrors: null
        };
    },
    
    getInitialState: function()
    {
        return {
            formData: this.props.formData || {},
            formErrors: this.props.formErrors || {}
        };
    },

    render: function()
    {
        var other = _.omit(this.props, ['onClose', 'onOpen']);
        
        var title = this.props.title;
        
        //Form
        var formSchema = this.props.formName && Panneau.Schema.Form[this.props.formName] ? Panneau.Schema.Form[this.props.formName]:{};
        var formProps = _.extend({
            token: $('meta[name=csrf-token]').attr('content'),
            fields: this.props.formFields,
            method: this.props.formMethod,
            action: this.props.formAction,
            data: this.state.formData,
            errors: this.state.formErrors
        }, formSchema);
        formProps.buttons = [];
        
        var FormComponent = Panneau.Components.Form.Component;
        
        return (
            <Modal {...other} onClose={this.onClose}>
                <div className="modal-header">
                    <button type="button" className="close" onClick={this.onClickClose} aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                    <h4 className="modal-title">{ title }</h4>
                </div>
                <div className="modal-body">
                    <FormComponent {...formProps} onSubmit={this.onSubmit} onChange={this.onFormChange} />
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-default" onClick={this.onClickSave}>Enregistrer</button>
                </div>
            </Modal>
        );

    },
    
    onClickSave: function(e)
    {
        this.onSubmit(e);
    },
    
    onClickClose: function(e)
    {
        e.preventDefault();
        
        this.props.closeModal(this.props.name);
    },
    
    onClose: function()
    {
        if(this.props.onClose)
        {
            this.props.onClose();
        }
    },
    
    onSubmit: function(e)
    {
        e.preventDefault();
        
        var token = document.querySelector('meta[name=csrf-token]').getAttribute('content');
        
        var formMethod = this.props.formMethod.toUpperCase();
        var methodName = formMethod.toLowerCase();
        if(methodName !== 'get')
        {
            methodName = 'post';
        }
        
        var $form = $(ReactDOM.findDOMNode(this)).find('form');
        var $inputs = $form.find('input, select, textarea');
        var data = {
            '_method': formMethod
        };
        $inputs.each(function(i, el)
        {
            var name = $(el).attr('name');
            if(name && name.length)
            {
                data[name] = $(el).val();
            }
        })
        
        var currentRequest = request[methodName](this.props.formAction)
            .set('X-CSRF-TOKEN', token)
            .set('Accept', 'application/json');
        
        for(var key in data)
        {
            currentRequest.field(key, data[key]);
        }
        
        currentRequest.end(_.bind(this.onFormComplete, this));
    },
    
    onFormComplete: function(err, response)
    {
        if(err)
        {
            this.onFormError(response.body)
            return;
        }
        
        this.props.closeModal();
        
        if(this.props.onComplete)
        {
            this.props.onComplete(response.body);
        }
    },
    
    onFormError: function(errors)
    {
        var formName = this.props.formName;
        var errorsByField = {};
        _.each(errors, function(error)
        {
            var matches = error.message.match(/\$([a-z][a-z0-9\[\]_-]+)/);
            if(matches)
            {
                _.set(errorsByField, matches[1], ['']);
            }
        });
        
        this.setState({
            formErrors: errorsByField
        });
    },
    
    onFormChange: function(data)
    {
        var currentValue = Immutable.fromJS(this.state.formData);
        var newValue = Immutable.fromJS(data);
        
        if(newValue !== currentValue)
        {
            var value = newValue.toJS();
            this.setState({
                formData: value
            });
        }
    }

});

module.exports = FormModal;



