var React = require('react-external');
var _ = require('lodash-external');
var $ = require('jquery-external');
var Immutable = require('immutable-external');

var Requests = require('../../requests/index');

var BubblesChannelList = require('./BubblesChannel.react');

var BubblesChannelFilters = React.createClass({
    
    propTypes: {
        channel: React.PropTypes.object,
        filters: React.PropTypes.array
    },
    
    getDefaultProps: function()
    {
        return {
            channel: null,
            filters: null
        };
    },
    
    getInitialState: function()
    {
        var filters = _.get(this.props, 'filters', null);
        
        return {
            filters: filters,
            filtersEnabled: filters && filters.length ? true:false
        };
    },

    render: function()
    {
        return (
            <div className="bubbles-channel-filters">
                <div className="form-group form-group-checkbox">
                    <label>
                        <input type="checkbox" checked={this.state.filtersEnabled} onChange={this.onFiltersCheckboxChange} /> Afficher seulement les lments correspondant aux critres suivants:
                    </label>
                </div>
                <BubblesChannelList 
                    channel={this.props.channel}
                    filtersEnabled={this.state.filtersEnabled}
                    filters={ _.isArray(this.state.filters) ? this.state.filters:[] }
                    onLoaded={this.onBubblesLoaded}
                    onFiltersChange={this.onBubblesFiltersChange}
                />
            </div>
        );

    },
    
    componentDidMount: function()
    {
        
    },
    
    componentDidUpdate: function(prevProps, prevState)
    {
        if(this.state.filters !== prevState.filters && (prevState.filtersEnabled || this.state.filtersEnabled))
        {
            console.log('SAVE FILTERS');
            this.saveFilters(this.state.filters);
        }
    },
    
    saveFilters: function(filters)
    {
        Requests.Screens.saveChannelSettings(this.props.screen, this.props.channel, {
            filters: filters
        }).then(_.bind(this.onSettingsSaved, this));
        
        if(this.props.onFiltersChange)
        {
            this.props.onFiltersChange(filters);
        }
    },
    
    onFiltersCheckboxChange: function(e)
    {
        var checked = e.currentTarget.checked;
        this.setState({
            filtersEnabled: checked,
            filters: !checked ? []:this.state.filters
        });
    },
    
    onBubblesLoaded: function(bubbles)
    {
        if(this.props.onBubblesLoaded)
        {
            this.props.onBubblesLoaded(bubbles);
        }
    },
    
    onBubblesFiltersChange: function(filters)
    {
        this.setState({
            filters: filters
        });
    },
    
    onSettingsSaved: function(channel)
    {
        var filters = _.get(channel, 'screen_settings.filters');
        var currentValue = Immutable.fromJS(this.props.channel);
        var newValue = currentValue.setIn(['screen_settings','filters'], filters);
        var newChannel = newValue.toJS();
        if(this.props.onChannelChange)
        {
            this.props.onChannelChange(newChannel);
        }
    }

});

module.exports = BubblesChannelFilters;



