var React = require('react-external');
var ReactDOM = require('react-dom-external');
var Panneau = require('panneau');
var ModalActions = Panneau.Actions.Modal;

var TeamItem = React.createClass({
    
    propTypes: {
        onChange: React.PropTypes.func,
        onRemove: React.PropTypes.func
    },
    
    getDefaultProps: function()
    {
        return {
            editable: false
        };
    },

    render: function()
    {
        var name = _.get(this.props, 'data.name');
        if(!name || !name.length)
        {
            name = _.get(this.props, 'data.email');
        }
        
        var role = _.get(this.props, 'data.role.name', null);
        var type = _.get(this.props, 'data.type', 'invitation');
        
        var link = URL.route('organisation.team.show', {
            user_id: _.get(this.props, 'data.id')
        });
        
        var imageStyle = {
            backgroundImage: 'url('+_.get(this.props, 'data.avatar.link', '/img/avatar.png')+')'
        };
        
        var description = <span>{ role }</span>;
        if(type === 'invitation')
        {
            description = <span>Invitation en cours...</span>;
        }
        
        var editable = this.props.editable;
        var thumbnail;
        if(editable)
        {
            thumbnail = (
                <a href={ link } className="thumbnail thumbnail-small thumbnail-horizontal" onClick={this.onClick}>
                    <span className="image" style={imageStyle}>&nbsp;</span>
                    <span className="caption">
                        <strong>{ name }</strong>
                        { description }
                    </span>
                </a>
            );
        }
        else
        {
            thumbnail = (
                <div className="thumbnail thumbnail-small thumbnail-horizontal">
                    <span className="image" style={imageStyle}>&nbsp;</span>
                    <span className="caption">
                        <strong>{ name }</strong>
                        { description }
                    </span>
                </div>
            );
        }
        
        return (
            <div className="list-item list-item-sm">
                { thumbnail }
            </div>
        );

    },
    
    onClick: function(e)
    {
        e.preventDefault();
        
        Panneau.dispatch(ModalActions.openModal('TeamEdit', {
            element: ReactDOM.findDOMNode(this),
            placement: 'auto',
            data: this.props.data,
            onComplete: _.bind(this.onModalComplete, this),
            onClose: _.bind(this.onModalClose, this)
        }));
    },
    
    onModalComplete: function(data)
    {
        if(data === null)
        {
            if(this.props.onRemove)
            {
                this.props.onRemove();
            }
            return;
        }
        
        if(this.props.onUpdate)
        {
            this.props.onUpdate(data);
        }
    },
    
    onModalClose: function()
    {
        
    }

});

module.exports = TeamItem;



