var React = require('react-external');
var _ = require('lodash-external');
var Panneau = require('panneau');

var SubFieldMixin = require('../../mixins/SubField.react');
var Map = require('../Map.react.js');

var PositionField = React.createClass({
    
    mixins: [SubFieldMixin],
    
    propTypes: {
        name: React.PropTypes.string,
        value: React.PropTypes.object,
        onChange: React.PropTypes.func
    },
    
    getDefaultProps: function()
    {
        return {
            
        };
    },
    
    render: function()
    {
        
        var inputLatitude = this.renderTextField('latitude', {
            label: 'Latitude',
            wrapperClassName: 'col-xs-6'
        });
        var inputLongitude = this.renderTextField('longitude', {
            label: 'Longitude',
            wrapperClassName: 'col-xs-6'
        });
        
        var latitude = _.get(this.props, 'value.latitude', null);
        var longitude = _.get(this.props, 'value.longitude', null);
        var radius = parseInt(_.get(this.props, 'value.radius', 0));
        
        return (
            <div className="form-group form-group-position">
                <div className="row">
                    <div className="col-sm-12">
                        <div className="row">
                            { inputLatitude }
                            { inputLongitude }
                        </div>
                    </div>
                    <div className="col-sm-12">
                        <Map zoom={17} latitude={parseFloat(latitude)} longitude={parseFloat(longitude)} radius={parseInt(radius)} onPositionChange={this.onMapPositionChange} onRadiusChange={this.onMapRadiusChange} />
                    </div>
                </div>
            </div>
        );
    },
    
    onMapPositionChange: function(position)
    {
        if(this.props.onChange)
        {
            this.props.onChange(position);
        }
    },
    
    onMapRadiusChange: function(value)
    {
        this.onSubFieldChange('radius', Math.round(value));
    }
    
    
});

module.exports = PositionField;



