
var ListActions = Panneau.Actions.List;

var Requests = require('../requests/index');

module.exports = {
    
    updateForScreen: function(screen_id)
    {
        return function(dispatch)
        {
            Requests.Timeline.loadForScreen(screen_id)
                .then(function(timeline)
                {
                    dispatch(ListActions.updateData(timeline, 'bubbles.timeline'));
                });
        };
    }
    
};



