var React = require('react-external');
var _ = require('lodash-external');

var OrganisationItem = React.createClass({
    
    getDefaultProps: function() {
        return {
            
        };
    },

    render: function()
    {    
        var name = _.get(this.props, 'data.name');
        var link = _.get(this.props, 'data.link');
        
        return (
            <div className="list-item">
                <a href={ link } className="thumbnail">
                    <span className="icon">
                        <span className="glyphicon glyphicon-picture"></span>
                    </span>
                    <span className="caption caption-lg">
                        <strong>{ name }</strong>
                    </span>
                </a>
            </div>
        );

    }

});

module.exports = OrganisationItem;



