var React = require('react-external');

var ChannelItem = require('../items/Channel.react');
var ChannelItemAdd = require('../items/ChannelAdd.react');

var Requests = require('../../requests/index');

var ListActions = Panneau.Actions.List;

var ChannelsList = React.createClass({
    
    mixins: [Panneau.Mixins.List],
    
    getDefaultProps: function() {
        return {
            screen: null
        };
    },

    render: function()
    {
        var items = this.renderListItems(this.props.items);
        
        return (
            <div className="list list-thumbnails list-channels">
                { items }
                <ChannelItemAdd channelsAdded={this.props.items} onAdd={this.onAdd} onRemove={this.onRemove} />
            </div>
        );

    },
    
    getListItemComponent: function()
    {
        return ChannelItem;
    },
    
    getListItemProps: function(channel, index)
    {
        var onChannelChange = _.bind(function(channel)
        {
            this.onChannelChange(channel, index);
        }, this);
        return {
            screen: this.props.screen,
            onChange: onChannelChange
        };
    },
    
    onAdd: function(channel)
    {
        Requests.Screens.addChannel(this.props.screen.id, channel.id)
                .then(_.bind(this.onChannelAdded, this));
    },
    
    onChannelChange: function(channel, index)
    {
        Panneau.dispatch(ListActions.updateItem(index, channel, this.props.name));
    },
    
    onChannelAdded: function(channel)
    {
        Panneau.dispatch(ListActions.addItem(channel, this.props.name));
    },
    
    onRemove: function(channel)
    {
        Requests.Screens.removeChannel(this.props.screen.id, channel.id)
                .then(_.bind(this.onChannelRemoved, this));
    },
    
    onChannelRemoved: function(channel)
    {
        var index = _.findIndex(this.props.items, function(it)
        {
            return it.id+'' === channel.id+'';
        });
        
        if(index >= 0)
        {
            Panneau.dispatch(ListActions.removeItem(index, this.props.name));
        }
    }

});

module.exports = ChannelsList;



