var React = require('react-external');
var _ = require('lodash-external');

var ConditionSnippet = React.createClass({
    
    getDefaultProps: function() {
        return {
            data: null
        };
    },

    render: function()
    {    
        var title = _.get(this.props, 'data.title');
        var description = _.get(this.props, 'data.description', '').split('\n');
        description = _.map(description, function(text, index)
        {
            var key = 'description_'+index;
            return (
                <div key={key}>{ text }</div>
            );
        });
        
        return (
            <div className="snippet snippet-condition">
                <div className="snippet-caption">
                    <h4 className="title">{ title }</h4>
                    <div className="description">
                        { description }
                    </div>
                </div>
            </div>
        );

    }

});

module.exports = ConditionSnippet;



