var React = require('react-external');
var Panneau = require('panneau');

var Requests = require('../requests/index');
var PictureField = Panneau.Components.Fields.Picture;

var UserAvatar = React.createClass({
    
    propTypes: {
        user: React.PropTypes.object.isRequired
    },
    
    getInitialState: function()
    {
        return {
            value: _.get(this.props, 'user.avatar')
        };
    },
    
    render: function()
    {
        return (
            <PictureField value={this.state.value} uploadUrl="/upload/picture" onChange={this.onChange} />
        )
    },
    
    onChange: function(picture)
    {
        if(picture === null)
        {
            Requests.Users.removeAvatar(this.props.user)
                    .then(_.bind(this.onPictureRemoved, this));
        }
        else
        {
            Requests.Users.updateAvatar(this.props.user, picture)
                    .then(_.bind(this.onPictureUpdated, this));
        }
    },
    
    onPictureUpdated: function(user)
    {
        this.setState({
            value: user.avatar
        });
    },
    
    onPictureRemoved: function()
    {
        this.setState({
            value: null
        });
    }
    
});

module.exports = UserAvatar;



