var React = require('react-external');

var CheckboxesField = Panneau.Components.Fields.Checkboxes;

var WeekdaysField = React.createClass({
    
    mixins: [ Panneau.Mixins.FieldErrors ],

    propTypes: {
        name: React.PropTypes.string
    },
    
    getDefaultProps: function()
    {
        return {
            name: 'weekdays',
            value: null,
            onChange: null,
            days: [
                {
                    label: 'Lundi',
                    value: '0'
                },
                {
                    label: 'Mardi',
                    value: '1'
                },
                {
                    label: 'Mercredi',
                    value: '2'
                },
                {
                    label: 'Jeudi',
                    value: '3'
                },
                {
                    label: 'Vendredi',
                    value: '4'
                },
                {
                    label: 'Samedi',
                    value: '5'
                },
                {
                    label: 'Dimanche',
                    value: '6'
                }
            ]
            
        };
    },

    render: function()
    {
        var label;
        if(this.props.label)
        {
            label = <label htmlFor={name} className="control-label">{this.props.label}</label>;
        }
        
        var formGroupClassName = 'form-group form-group-weekdays';
        if(this.props.errors)
        {
            formGroupClassName += ' has-error';
        }
        
        var errors = this.renderErrors(this.props.errors);
        
        return (
            <div className={formGroupClassName}>
                { label }
                <CheckboxesField type="checkbox.button" value={this.props.value} values={this.props.days} onChange={this.onChange} />
                { errors }
            </div>
        );
    },
    
    onChange: function(value)
    {
        if(this.props.onChange)
        {
            this.props.onChange(value);
        }
    }

});

module.exports = WeekdaysField;



