var React = require('react-external');

var PlaylistItem = require('../items/PlaylistItem.react');
var PlaylistItemAdd = require('../items/PlaylistItemAdd.react');

var ModalActions = Panneau.Actions.Modal;
var ListActions = Panneau.Actions.List;
var TimelineActions = require('../../actions/TimelineActions');
var Requests = require('../../requests/index');

var Requests = require('../../requests/index');

var PlaylistList = React.createClass({
    
    mixins: [
        Panneau.Mixins.List,
        Panneau.Mixins.Paginator,
        Panneau.Mixins.Sortable
    ],
    
    sortableOptions: {
        ref: 'list',
        model: 'items',
        handle: '.list-item-col-drag',
        filter: '.list-item-add',
        draggable: '.list-item',
        onUpdate: 'onSortableUpdate'
    },
    
    propTypes: {
        screen: React.PropTypes.object,
        screen: React.PropTypes.object
    },
    
    getDefaultProps: function()
    {
        return {
            addButton: true,
            emptyMessage: null,
            screen: null,
            playlist: null
        };
    },

    render: function()
    {
        var channelsParams = {};
        var screen_id = _.get(this.props, 'screen.id', null);
        if(screen_id)
        {
            channelsParams.screen_id = screen_id;
        }
        
        var items = this.renderListItems(this.props.items);
        
        //Add button
        var addButton = null;
        if(this.props.addButton)
        {
            addButton = <PlaylistItemAdd 
                            bubblesSelectChannelsParams={channelsParams} 
                            playlist={this.props.playlist} 
                            currentItems={this.props.items} 
                            onAdd={this.onAdd} />
        }
        
        if(!items.length && this.props.emptyMessage)
        {
            items = (
                <div className="list-item list-item-empty">
                    { this.props.emptyMessage }
                </div>
            );
        }
        
        var className = 'list list-rows list-playlist';
        
        return (
            <div className={className}>
                <div ref="list" className="list-items">
                    { items }
                </div>
                { addButton }
            </div>
        );
    },
    
    getListItemComponent: function(it, index)
    {
        return PlaylistItem;
    },
    
    getListItemProps: function(it, index, itemProps)
    {
        var props = {};
        props.onClickRemove = _.bind(function()
        {
            this.onItemClickRemove(it, index);
        }, this);
        props.onConditionChange = _.bind(function(condition)
        {
            this.onItemConditionChange(condition, it, index);
        }, this);
        return props;
    },
    
    onItemClickRemove: function(it, index)
    {
        if(!confirm('tes-vous certain de vouloir enlever ce contenu?'))
        {
            return;
        }
        
        var playlist_id = _.get(this.props, 'playlist.id');
        Requests.Playlists.removeItem(playlist_id, it.id)
            .then(_.bind(this.onItemRemoved, this));
    },
    
    onItemConditionChange: function(condition, it, index)
    {
        var screen_id = _.get(this.props, 'screen.id');
        it.condition = condition;
        Panneau.dispatch(ListActions.updateItem(index, it, this.props.name));
        Panneau.dispatch(TimelineActions.updateForScreen(screen_id));
    },
    
    onAdd: function(items)
    {
        var screen_id = _.get(this.props, 'screen.id', null);
        Panneau.dispatch(ListActions.addItems(items, this.props.name));
        Panneau.dispatch(TimelineActions.updateForScreen(screen_id));
    },
    
    onSortableUpdate: function(e)
    {
        var ids = [];
        _.each(this.state.items, function(it)
        {
            ids.push(it.id);
        });
        
        var playlist_id = _.get(this.props, 'playlist.id');
        Requests.Playlists.updateOrder(playlist_id, ids)
            .then(_.bind(this.onOrderUpdated, this));
            
        Panneau.dispatch(ListActions.updateData(this.state.items, this.props.name));
    },
    
    onItemRemoved: function(items)
    {
        var screen_id = _.get(this.props, 'screen.id');
        Panneau.dispatch(ListActions.updateData(items, this.props.name));
        Panneau.dispatch(TimelineActions.updateForScreen(screen_id));
    },
    
    onOrderUpdated: function(bubbles)
    {
        var screen_id = _.get(this.props, 'screen.id', null);
        Panneau.dispatch(ListActions.updateData(bubbles, this.props.name));
        Panneau.dispatch(TimelineActions.updateForScreen(screen_id));
    }

});

module.exports = PlaylistList;



