var React = require('react-external');

var ScreenItem = require('../items/Screen.react');
var ScreenAdd = require('../items/ScreenAdd.react');

var ListActions = Panneau.Actions.List;

var ScreensList = React.createClass({
    
    mixins: [Panneau.Mixins.List],
    
    getDefaultProps: function() {
        return {
            
        };
    },

    render: function()
    {
        var items = this.renderListItems(this.props.items);
        
        return (
            <div className="list list-thumbnails list-screens">
                { items }
                <ScreenAdd onAdd={this.onAdd} />
            </div>
        );

    },
    
    getListItemComponent: function()
    {
        return ScreenItem;
    },
    
    onAdd: function(screen)
    {
        Panneau.dispatch(ListActions.addItemIfUnique(screen, 'id', this.props.name));
    }

});

module.exports = ScreensList;



