var React = require('react-external');

var RoleField = React.createClass({
    
    mixins: [ Panneau.Mixins.FieldErrors ],

    propTypes: {
        name: React.PropTypes.string
    },
    
    getDefaultProps: function()
    {
        return {
            name: 'role',
            value: null,
            onChange: null
        };
    },

    render: function()
    {
        var label;
        if(this.props.label)
        {
            label = <label htmlFor={name} className="control-label">{this.props.label}</label>;
        }
        
        var select = this.renderSelect();
        
        var formGroupClassName = 'form-group form-group-role';
        if(this.props.errors)
        {
            formGroupClassName += ' has-error';
        }
        
        var errors = this.renderErrors(this.props.errors);
        
        return (
            <div className={formGroupClassName}>
                { label }
                { select }
                { errors }
                <div className="description">
                    
                </div>
            </div>
        );
    },
    
    renderSelect: function()
    {
        var selectProps = _.omit(this.props, ['onChange', 'label'])
        var Select = Panneau.Components.Fields.Select;
        return (
            <Select {...selectProps} onChange={this.onChange}></Select>
        )
    },
    
    onChange: function(value)
    {
        
        if(this.props.onChange)
        {
            this.props.onChange(value);
        }
    }

});

module.exports = RoleField;



