var React = require('react-external');
var _ = require('lodash-external');
var $ = require('jquery-external');
var Immutable = require('immutable-external');

var BubblesList = require('./Bubbles.react');

var Form = Panneau.Components.Form.Component;
var Paginator = Panneau.Components.Paginator;

var Requests = require('../../requests/index');

var BubblesChannelList = React.createClass({
    
    propTypes: {
        channel: React.PropTypes.object,
        filters: React.PropTypes.array,
        filtersEnabled: React.PropTypes.bool,
        checkedBubbles: React.PropTypes.array,
        onLoaded: React.PropTypes.func,
        onFiltersChange: React.PropTypes.func,
        onChange: React.PropTypes.func,
        onClickAdd: React.PropTypes.func
    },
    
    getDefaultProps: function()
    {
        return {
            filtersEnabled: true,
            channel: null,
            checkedBubbles: null,
            filters: null,
            paginatorBottom: true
        };
    },
    
    getInitialState: function()
    {
        var filters = this.props.filters ? Immutable.fromJS(this.props.filters):null;
        var params = this.props.filters ? Immutable.fromJS(this.getParamsFromFilters(this.props.filters)):null;
        
        return {
            checkedBubbles: this.props.checkedBubbles,
            bubbles: null,
            params: params,
            page: 1,
            filters: filters,
            loading: false
        };
    },
    
    render: function()
    {
        if(!this.state.bubbles)
        {
            var loading = this.renderLoading();
            return (
                <div className="bubbles-selector">
                    { loading }
                </div>
            );
        }
        
        //Filters form
        var filtersForm = null;
        if(this.props.channel)
        {
            var filters = this.state.filters ? this.state.filters.toJS():null;
            var filtersFields = this.props.channel.bubbles_filters;
            filtersForm = this.renderFiltersForm(filters, filtersFields);
        }
        
        //Paginator
        var paginator = null;
        if(this.state.pagination)
        {
            paginator = this.renderPaginator(this.state.pagination);
        }
        
        //Bubbles list
        var bubbles = this.state.bubbles || [];
        var props = _.omit(this.props, ['items', 'checkedBubbles', 'onBubbleCheck', 'onBubbleClickAdd']);
        var list = (
            <BubblesList 
                {...props}
                items={ bubbles } 
                checkedBubbles={ this.state.checkedBubbles }
                onBubbleCheck={ this.onBubbleCheck } 
                onBubbleClickAdd={ this.onBubbleClickAdd }
            />
        );
        
        //Loading
        var loading = null;
        if(this.state.loading)
        {
            loading = this.renderLoading();
        }

        return (
            <div className="bubbles-channel">
                { filtersForm }
                { paginator }
                { loading }
                { list }
                { this.props.paginatorBottom && bubbles.length > 5 ? paginator:null }
            </div>
        );

    },
    
    renderLoading: function()
    {
        return (
            <div className="loading-block">
                <div className="table">
                    <div className="middle">Chargement...</div>
                </div>
                <div className="block"></div>
            </div>
        );
    },
    
    renderFiltersForm: function(filters, fieldsFilters)
    {
        var formData = {
            filters: filters
        };
        var formFields = [
            {
                'name': 'filters',
                'type': 'filters',
                'filters': fieldsFilters
            }
        ];
        
        var block;
        if(!this.props.filtersEnabled)
        {
            block = (
                <div className="filters-block"></div>
            );
        }
        
        return (
            <div className="filters">
                <Form name="bubbles.filters" fields={formFields} data={formData} onFieldChange={this.onFiltersFormFieldChange} />
                { block }
            </div>
        );
    },
    
    renderPaginator: function(pagination)
    {
        var bubblesCount = pagination.total;
        var countLabel;
        if(bubblesCount === 1)
        {
            countLabel = '1 lment correspondant  ces critres';
        }
        else if(bubblesCount > 1)
        {
            countLabel = bubblesCount+' lments correspondants  ces critres';
        }
        
        if(countLabel)
        {
            var paginator = null;
            if(pagination.last_page > 1)
            {
                paginator = (
                    <Paginator 
                        total={pagination.total}
                        perPage={pagination.per_page}
                        currentPage={pagination.current_page}
                        lastPage={pagination.last_page}
                        interval={3}
                        onClickPage={this.onPaginatorClickPage}
                        onClickNext={this.onPaginatorClickPage}
                        onClickPrevious={this.onPaginatorClickPage} />
                );
            }
            
            countLabel = (
                <div className="bubbles-paginator">
                    <div className="col col-count">
                        { countLabel }
                    </div>
                    <div className="col col-paginator">
                        { paginator }
                    </div>
                </div>
            );
        }
        
        return countLabel;
    },
    
    componentWillReceiveProps: function(nextProps)
    {
        if(this.props.filters !== nextProps.filters)
        {
            var filters = nextProps.filters ? Immutable.fromJS(nextProps.filters):null;
            var params = nextProps.filters ? Immutable.fromJS(this.getParamsFromFilters(nextProps.filters)):null;
            
            this.setState({
                filters: filters,
                params: params
            })
        }
    },
    
    componentDidUpdate: function(prevProps, prevState)
    {
        if(this.queryHasChanged(prevProps, prevState))
        {
            var reset = this.channelHasChanged(prevProps, prevState);
            this.reloadBubbles(reset);
        }
    },
    
    componentDidMount: function()
    {
        this.reloadBubbles();
    },
    
    channelHasChanged: function(prevProps, prevState)
    {
        var channelId = _.get(this.props, 'channel.id', '')+'';
        var prevChannelId = _.get(prevProps, 'channel.id', '')+'';
        
        return channelId !== prevChannelId;
    },
    
    queryHasChanged: function(prevProps, prevState)
    {
        return (
            this.channelHasChanged(prevProps, prevState) || 
            this.state.params !== prevState.params || 
            this.state.page !== prevState.page
        ) ? true:false;
    },
    
    reloadBubbles: function(reset)
    {
        if(typeof(reset) === 'undefined')
        {
            reset = false;
        }
        
        var params = this.state.params ? this.state.params.toJS():null;
        if(reset)
        {
            this.setState({
                params: null,
                filters: null,
                page: 1,
                pagination: null,
                checked: null
            }, function()
            {
                this.loadBubbles(this.props.channel, params);
            });
            
        }
        else
        {
            this.loadBubbles(this.props.channel, params);
        }
    },
    
    loadBubbles: function(channel, params)
    {
        this.setState({
            loading: true
        }, function()
        {
            if(typeof(params) === 'undefined' || !params)
            {
                params = {};
            }
            params.page = this.state.page;
            Requests.Bubbles.loadForChannel(channel, params)
                .then(this.onBubblesLoaded);
        });
    },
    
    getParamsFromFilters: function(filters)
    {
        var params = {};
        var hasParams = false;
        _.each(filters || [], function(filter)
        {
            var value = null;
            if(_.isObject(filter.value) && !_.isArray(filter.value))
            {
                value = _.get(filter, 'value.value');
            }
            else if(filter.value)
            {
                value = filter.value;
            }
            if(value !== null && typeof(value) !== 'undefined' && value.length)
            {
                params['filter_'+filter.name] = _.isArray(value) ? value.join(','):value;
                hasParams = true;
            }
        });
        return hasParams ? params:null;
    },
    
    onBubblesLoaded: function(bubbles)
    {
        this.setState({
            bubbles: bubbles.items,
            pagination: bubbles.pagination,
            loading: false
        }, function()
        {
            if(this.props.onLoaded)
            {
                this.props.onLoaded(bubbles);
            }
        });
    },
    
    onPaginatorClickPage: function(e, page)
    {
        e.preventDefault();
        
        this.setState({
            page: page
        });
    },
    
    onBubbleCheck: function(bubble, checked)
    {
        var checkedBubbles = this.state.checkedBubbles || [];
        var current = _.find(checkedBubbles, function(it)
        {
            return (it.id+'') === (bubble.id+'');
        });
        
        if(!current)
        {
            checkedBubbles.push(bubble);
        }
        else
        {
            checkedBubbles = _.filter(checkedBubbles, function(it)
            {
                return (it.id+'') !== (current.id+'');
            });
        }
        
        this.setState({
            checkedBubbles: checkedBubbles
        }, function()
        {
            if(this.props.onChange)
            {
                this.props.onChange(this.state.checkedBubbles);
            }
        });
    },
    
    onFiltersFormFieldChange: function(name, value)
    {
        if(name === 'filters')
        {
            var bubblesParams = this.getParamsFromFilters(value);
            this.setState({
                params: bubblesParams ? Immutable.fromJS(bubblesParams):null,
                filters: value ? Immutable.fromJS(value): null
            });
            
            if(this.props.onFiltersChange)
            {
                this.props.onFiltersChange(value);
            }
        }
    },
    
    onBubbleClickAdd: function(bubble)
    {
        if(this.props.onClickAdd)
        {
            this.props.onClickAdd(bubble);
        }
    }

});

module.exports = BubblesChannelList;



