var Immutable = require('immutable-external');
var _ = require('lodash-external');

var FormComponent = Panneau.Components.Form.Component;

var SubForm = {

    renderForm: function(props)
    {
        props.data = this.state.formData ? this.state.formData.toJS():null;
        props.errors = this.state.formErrors ? this.state.formErrors.toJS():null;
        props.token = document.querySelector('meta[name=csrf-token]').getAttribute('content');
        
        return (
            <FormComponent {...props} onSubmit={this.onSubmit} onFieldChange={this.onFormFieldChange} />
        );
    },
    

    updateFormErrors: function(errors)
    {
        var formName = this.props.formName;
        var errorsByField = {};
        _.each(errors, function(error)
        {
            if(error.message === 'validation')
            {
                errorsByField = _.merge(errorsByField, error.validation);
            }
            else
            {
                var matches = error.message.match(/\$([a-z][a-z0-9\[\]_-]+)/);
                if(matches)
                {
                    var message = 
                        error.message.match(/^\$[a-z][^\s]+\:?\s*/) ?
                        error.message.replace(/^\$[a-z][^\s]+\:?\s*/, ''):
                        '';
                    _.set(errorsByField, matches[1], [message]);
                }
            }
        });
        
        var newErrors = Immutable.fromJS(errorsByField);
        
        this.setState({
            formErrors: newErrors
        });
    },
    
    onFormFieldChange: function(name, value)
    {
        var data = this.state.formData;
        if(!data)
        {
            data = Immutable.fromJS({});
        }
        var newData = data.set(name, value);
        
        this.setState({
            formData: newData
        });
    }
    
};


module.exports = SubForm;



