var React = require('react-external');
var _ = require('lodash-external');
var Panneau = require('panneau');

var SubFieldMixin = require('../../mixins/SubField.react');
var PositionField = require('./Position.react');
var gmap = require('../../lib/gmap');

var AddressField = React.createClass({
    
    mixins: [SubFieldMixin],
    
    propTypes: {
        name: React.PropTypes.string,
        value: React.PropTypes.object,
        onChange: React.PropTypes.func
    },
    
    getDefaultProps: function()
    {
        return {
            withPosition: true
        };
    },
    
    render: function()
    {
        
        var inputNumber = this.renderTextField('number', {
            label: 'Numro',
            wrapperClassName: 'col-xs-4 col-md-4'
        });
        var inputStreet = this.renderTextField('street', {
            label: 'Rue',
            wrapperClassName: 'col-xs-8 col-md-8'
        });
        var inputCity = this.renderTextField('city', {
            label: 'Ville',
            wrapperClassName: 'col-xs-8 col-md-8'
        });
        var inputPostalcode = this.renderTextField('postalcode', {
            label: 'Code postal',
            wrapperClassName: 'col-xs-4 col-md-4'
        });
        
        var positionValue = _.get(this.props, 'value.position', {});
        var positionName = this.props.name+'[position]';
        var position = null;
        if(this.props.withPosition)
        {
            position = (
                <PositionField name={positionName} value={ positionValue } onChange={this.onPositionChange} />
            );
        }
        
        return (
            <div className="form-group form-group-address">
                <div className="row">
                    { inputNumber }
                    { inputStreet }
                    { inputCity }
                    { inputPostalcode }
                </div>
                { position }
            </div>
        );
    },
    
    checkAddress: _.debounce(function(value)
    {
        if(!value.number || !value.street || !value.street.length)
        {
            return;
        }
        
        var address = [
            value.number+' '+value.street
        ];
        if(value.city && value.city.length)
        {
            address.push(value.city);
        }
        else
        {
            address.push('Montreal');
        }
        if(value.postalcode && value.postalcode.length)
        {
            address.push(value.postalcode);
        }
        
        gmap.loadGMap().then(function()
        {
            return gmap.geocode(address.join(', '));
        })
        .then(_.bind(this.onGeocode, this))
    }, 300),
    
    onGeocode: function(latLng)
    {
        this.onSubFieldChange('position', {
            latitude: latLng.lat(),
            longitude: latLng.lng()
        });
    },
    
    onPositionChange: function(value)
    {
        var currentValue = Immutable.fromJS(this.props.value || {});
        var newValue = currentValue.mergeIn(['position'], value);
        
        if(currentValue !== newValue)
        {
            var value = newValue.toJS();
            
            if(this.props.onChange)
            {
                this.props.onChange(value);
            }
        }
    },
    
    onChange: function(value)
    {
        var latitude = _.get(value, 'position.latitude', null);
        var longitude = _.get(value, 'position.longitude', null);
        if(!latitude && !longitude)
        {
            this.checkAddress(value);
        }
    }
    
    
});

module.exports = AddressField;



