var React = require('react-external');
var ReactDOM = require('react-dom-external');

var ScreenActions = require('../../actions/ScreenActions');
var ModalActions = Panneau.Actions.Modal;

var ScreenAddItem = React.createClass({
    
    getDefaultProps: function() {
        return {
            
        };
    },

    render: function()
    {
        return (
            <div className="list-item list-item-sm list-item-add">
                <a href="#" className="thumbnail" onClick={this.onClick}>
                    <span className="icon">
                        <span className="glyphicon glyphicon-plus btn-icon-screen"></span>
                    </span>
                    
                    <span className="caption caption-lg">
                        <span className="small">
                            Ajouter un cran
                        </span>
                    </span>
                </a>
            </div>
        );

    },
    
    onClick: function(e)
    {
        e.preventDefault();
        
        Panneau.dispatch(ModalActions.openModal('ScreenLink', {
            element: ReactDOM.findDOMNode(this),
            onComplete: this.onModalComplete,
            onClose: this.onModalClose
        }));
    },
    
    onModalComplete: function(screen)
    {
        if(this.props.onAdd)
        {
            this.props.onAdd(screen);
        }
    },
    
    onModalClose: function()
    {
        
    }
    
});

module.exports = ScreenAddItem;



