var React = require('react-external');

var Bubbles = require('./Bubbles.react');

var ModalActions = Panneau.Actions.Modal;
var ListActions = Panneau.Actions.List;
var TimelineActions = require('../../actions/TimelineActions');

var Requests = require('../../requests/index');

var BubblesPlaylistList = React.createClass({
    
    propTypes: {
        screen_id: React.PropTypes.number,
        playlist_id: React.PropTypes.number
    },
    
    getDefaultProps: function()
    {
        return {
            layout: "playlist",
            addButton: true,
            emptyMessage: null,
            screen_id: null,
            playlist_id: null
        };
    },
    
    getInitialState: function()
    {
        return {
            bubblesToAdd: null,
            filtersToAdd: null
        };
    },

    render: function()
    {
        var channelsParams = {};
        if(this.props.screen_id)
        {
            channelsParams.screen_id = this.props.screen_id;
        }
        
        return (
            <Bubbles 
                {...this.props} 
                bubblesSelectChannelsParams={channelsParams} 
                onAdd={this.onAdd} 
                onBubbleClickRemove={this.onBubbleClickRemove}
                onOrderChange={this.onOrderChange}
                onBubbleConditionChange={this.onBubbleConditionChange}
            />
        );
    },
    
    onAdd: function(data)
    {
        var bubbles = _.get(data, 'bubbles', null);
        var filters = _.get(data, 'filters', null);
        
        this.setState({
            filtersToAdd: filters,
            bubblesToAdd: bubbles
        }, function()
        {
            Panneau.dispatch(ModalActions.openModal('Conditions', {
                bubbles: bubbles,
                filters: filters,
                onComplete: this.onModalComplete,
                onClose: this.onModalClose
            }));
        });
    },
    
    onBubbleClickRemove: function(bubble, index)
    {
        if(!confirm('tes-vous certain de vouloir enlever ce contenu?'))
        {
            return;
        }
        
        Requests.Playlists.removeBubble(this.props.playlist_id, index)
            .then(_.bind(function(bubbles)
            {
                this.onBubbleRemoved(bubbles);
            }, this));
    },
    
    onModalComplete: function(condition)
    {
        if(this.state.bubblesToAdd)
        {
            Requests.Playlists.addBubblesWithCondition(this.props.playlist_id, this.state.bubblesToAdd, condition)
                .then(_.bind(this.onBubbleAdded, this));
        }
        else if(this.state.filtersToAdd)
        {
            Requests.Playlists.addFiltersWithCondition(this.props.playlist_id, this.state.filtersToAdd, condition)
                .then(_.bind(this.onBubbleAdded, this));
        }
    },
    
    onOrderChange: function(items)
    {
        var ids = [];
        _.each(items, function(it)
        {
            ids.push(it.playlist_item_id);
        });
        
        Requests.Playlists.updateOrder(this.props.playlist_id, ids)
            .then(_.bind(this.onOrderUpdated, this));
    },
    
    onModalClose: function()
    {
        this.setState({
            filtersToAdd: null,
            bubblesToAdd: null
        });
    },
    
    onBubbleRemoved: function(bubbles)
    {
        Panneau.dispatch(ListActions.updateData(bubbles, this.props.name));
        Panneau.dispatch(TimelineActions.updateForScreen(this.props.screen_id));
    },
    
    onBubbleAdded: function(bubbles)
    {
        bubbles = !_.isArray(bubbles) ? [bubbles]:bubbles;
        this.setState({
            filtersToAdd: null,
            bubblesToAdd: null
        }, function()
        {
            Panneau.dispatch(ListActions.addItems(bubbles, this.props.name));
            Panneau.dispatch(ModalActions.closeModal('Conditions'));
            Panneau.dispatch(TimelineActions.updateForScreen(this.props.screen_id));
        });
    },
    
    onBubbleConditionChange: function()
    {
        Panneau.dispatch(TimelineActions.updateForScreen(this.props.screen_id));
    },
    
    onOrderUpdated: function(bubbles)
    {
        Panneau.dispatch(ListActions.updateData(bubbles, this.props.name));
        Panneau.dispatch(TimelineActions.updateForScreen(this.props.screen_id));
    }

});

module.exports = BubblesPlaylistList;



