var React = require('react-external');
var _ = require('lodash-external');
var $ = require('jquery-external');
var Immutable = require('immutable-external');

var FormActions = Panneau.Actions.Form;
var Modal = Panneau.Components.Modals.Modal;
var Form = Panneau.Components.Form.Component;

var Requests = require('../../requests/index');

var ConditionsModal = React.createClass({
    
    contextTypes: {
        store: React.PropTypes.object
    },
    
    getDefaultProps: function()
    {
        return {
            visible: false,
            condition: null
        };
    },
    
    getInitialState: function()
    {
        var condition = _.get(this.props, 'condition.fields', {});
        var filteredCondition = {};
        _.each(condition, function(val, key)
        {
            if(val !== null)
            {
                filteredCondition[key] = val;
            }
        });
        
        return {
            condition: filteredCondition
        };
    },

    render: function()
    {
        var formFields = [
            {
                'name' : 'days',
                'type' : 'condition_weekdays'
            },
            {
                'name' : 'daterange',
                'type' : 'condition_daterange'
            },
            {
                'name' : 'date',
                'type' : 'condition_date'
            },
            {
                'name' : 'time',
                'type' : 'condition_time'
            }
        ];
        
        var formData = this.state.condition || {};
        
        return (
            <Modal closeModal={this.props.closeModal} visible={this.props.visible} onClose={this.onClose}>
                <div className="modal-header">
                    <button type="button" className="close" onClick={this.onClickClose} aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                    <h4 className="modal-title">Conditions d'affichage</h4>
                </div>
                <div className="modal-body">
                    <Form name="condition" data={formData} fields={formFields} onFieldChange={this.onFormFieldChange} />
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-default" onClick={this.onClickClose}>Annuler</button>
                    <button type="button" className="btn btn-primary" onClick={this.onClickSave}>Enregistrer</button>
                </div>
            </Modal>
        );

    },
    
    onFormFieldChange: function(name, value)
    {
        var currentValue = Immutable.fromJS(this.state.condition || {});
        
        var newValue;
        if(value === null)
        {
            newValue = currentValue.delete(name);
        }
        else
        {
            newValue = currentValue.set(name, value);
        }
        
        if(currentValue !== newValue)
        {
            this.setState({
                condition: newValue.toJS()
            });
        }
    },
    
    onClickClose: function()
    {
        if(this.props.closeModal)
        {
            this.props.closeModal();
        }
        else
        {
            this.close();
        }
    },
    
    onClickSave: function()
    {
        var id = _.get(this.props, 'condition.id', null);
        Requests.Playlists.saveCondition(this.state.condition, id)
            .then(_.bind(this.onConditionsSaved, this))
    },
    
    onClose: function()
    {
        if(this.props.onClose)
        {
            this.props.onClose();
        }
    },
    
    onConditionsSaved: function(it)
    {
        if(this.props.onComplete)
        {
            this.props.onComplete(it);
        }
    }

});

module.exports = ConditionsModal;



