var _ = require('lodash-external');

var TextField = Panneau.Components.Fields.Text;

var SubField = {

    renderTextField: function(key, props)
    {
        return this.renderSubField(TextField, key, props);
    },
    
    renderSubField: function(FieldComponent, key, props)
    {
        var name = this.props.name+'['+key+']';
        var value = _.get(this.props, 'value.'+key, null);
        
        props = _.extend({
            wrapper: true,
            wrapperClassName: ''
        }, props);
        
        var onChange = _.bind(function(value)
        {
            this.onSubFieldChange(key, value);
        }, this);
        
        var field = (
            <FieldComponent name={name} value={value} onChange={onChange} {...props} />
        );
        
        if(props.wrapper)
        {
            return (
                <div className={ props.wrapperClassName }>
                    { field }
                </div>
            );
        }
        else
        {
            return field;
        }
    },
    
    onSubFieldChange: function(key, value)
    {
        var currentValue = Immutable.fromJS(this.props.value || {});
        var newValue = currentValue.set(key, value);
        
        if(currentValue !== newValue)
        {
            var value = newValue.toJS();
            
            if(this.onChange)
            {
                this.onChange(value);
            }
            
            if(this.props.onChange)
            {
                this.props.onChange(value);
            }
        }
    }
    
};


module.exports = SubField;



