var React = require('react-external');
var _ = require('lodash-external');

var URL = require('../../lib/url');

var ModalActions = Panneau.Actions.Modal;

var ChannelItem = React.createClass({
    
    getDefaultProps: function()
    {
        return {
            
        };
    },

    render: function()
    {    
        var title = _.get(this.props, 'data.snippet.title');
        var description = _.get(this.props, 'data.snippet.description');
        var picture = _.get(this.props, 'data.snippet.picture.link');
        var imageStyle = {
            backgroundImage: picture ? ('url('+picture+')'):'none'
        };
        
        var url = URL.route('organisation.screens.channel', {
            screen_id: this.props.screen.id,
            channel_id: this.props.data.id
        });
        
        return (
            <div className="list-item list-item-xs list-item-channel">
                <a href={url} className="thumbnail" onClick={this.onClick}>
                    <span className="image" style={imageStyle}></span>
                    <span className="caption">{ title }</span>
                </a>
            </div>
        );

    },
    
    onClick: function(e)
    {
        /*e.preventDefault();
        
        Panneau.dispatch(ModalActions.openModal('ChannelSettings', {
            channel: this.props.data,
            screen: this.props.screen,
            onClose: this.onModalClose,
            onChannelChange: this.onChannelChange
        }));*/
    },
    
    onModalClose: function()
    {
        
    },
    
    onChannelChange: function(channel)
    {
        if(this.props.onChange)
        {
            this.props.onChange(channel);
        }
    }

});

module.exports = ChannelItem;



