var React = require('react-external');
var _ = require('lodash-external');
var Immutable = require('immutable-external');

var SelectField = Panneau.Components.Fields.Select;

var FilterSelectField = React.createClass({

    propTypes: {
        name: React.PropTypes.string
    },
    
    getDefaultProps: function()
    {
        return {
            name: 'filter_select',
            value: null,
            onChange: null
        };
    },

    render: function()
    {
        var value = _.get(this.props, 'value', null);
        var props = _.cloneDeep(_.omit(this.props, ['name', 'value', 'label', 'onChange']));
        if(props.values)
        {
            props.values.unshift({
                value: null,
                label: 'Slectionnez...'
            });
        }
        
        return (
            <div className="form-group form-group-filter-select form-group-row">
                <div className="form-group-inline form-group-label">
                    est
                </div>
                <div className="form-group-inline">
                    <SelectField {...props} name={this.props.name} value={value} onChange={this.onSelectChange} />
                </div>
            </div>
        );
    },
    
    onSelectChange: function(value)
    {
        if(this.props.onChange)
        {
            this.props.onChange(value);
        }
    }

});

module.exports = FilterSelectField;



