var React = require('react-external');
var Immutable = require('immutable-external');
var _ = require('lodash-external');
var $ = require('jquery-external');

var FormActions = Panneau.Actions.Form;
var ModalActions = Panneau.Actions.Modal;
var Requests = require('../../requests/index');
var ScreenActions = require('../../actions/ScreenActions');
var Popover = Panneau.Components.Modals.Popover;

var SubFormMixins = require('../../mixins/SubForm.react');

var ScreenLinkModal = React.createClass({
    
    mixins: [SubFormMixins],
    
    getDefaultProps: function()
    {
        return {
            visible: false,
            element: null,
            formName: 'organisation.screens.link'
        };
    },
    
    getInitialState: function()
    {
        return {
            formData: null,
            formErrors: null
        };
    },

    render: function()
    {
        var other = _.omit(this.props, ['onClose', 'authCode']);
        var title = 'Lier un cran';
        
        //Form
        var formProps = Panneau.Schema.Form[this.props.formName];
        formProps.name = this.props.formName;
        formProps.onSubmit = this.onSubmit;
        var formContainer = this.renderForm(formProps);
        
        return (
            <Popover {...other} title={title} onClose={this.onClose}>
                <p>Assurez-vous que lcran soit allum et connect  internet.</p>
                { formContainer }
                <p>Ce code de 4 chiffres apparat  lcran lors de sa premire mise en marche.</p>
            </Popover>
        );

    },
    
    
    componentDidMount: function()
    {
        _.bindAll(this, 'onScreenLinked', 'onScreenLinkError');
    },
    
    onClose: function()
    {
        if(this.props.onClose)
        {
            this.props.onClose();
        }
    },
    
    onSubmit: function(e)
    {
        e.preventDefault();
        
        var authCode = this.state.formData ? this.state.formData.get('auth_code'):null;
        Requests.Organisations.linkScreen(authCode)
                .then(this.onScreenLinked, this.onScreenLinkError);
    },
    
    onScreenLinked: function(screen)
    {
        this.props.closeModal();
        
        if(this.props.onComplete)
        {
            this.props.onComplete(screen);
        }
    },
    
    onScreenLinkError: function(errors)
    {
        this.updateFormErrors(errors);
    }

});

module.exports = ScreenLinkModal;



