var React = require('react-external');
var _ = require('lodash-external');
var Immutable = require('immutable-external');

var DateField = Panneau.Components.Fields.Date;
var DateRangeField = Panneau.Components.Fields.DateRange;

var FilterDateField = React.createClass({

    propTypes: {
        name: React.PropTypes.string,
        types: React.PropTypes.array.isRequired
    },
    
    getDefaultProps: function()
    {
        return {
            name: 'filter_date',
            value: null,
            onChange: null,
            types: [
                {
                    'name': 'future',
                    'label': 'dans le futur'
                },
                {
                    'name': 'date',
                    'label': ' la date'
                },
                {
                    'name': 'range',
                    'label': 'entre les dates'
                }
            ]
        };
    },

    render: function()
    {
        var value = _.get(this.props, 'value', null);
        var valueType = value === 'future' ? value:(_.isArray(value) ? 'range':(value !== null ? 'date':null));
        console.log(valueType);
        var type = _.find(this.props.types, function(type)
        {
            return type.name === valueType;
        });
        var typeLabel = 'Slectionnez...';
        if(type)
        {
            typeLabel = type.label;
        }
        
        var types = this.renderTypes(this.props.types);
        
        var dateField;
        if(type && type.name === 'date')
        {
            dateField = (
                <div className="form-group-inline">
                    <DateField value={value} onChange={this.onDateChange} />
                </div>
            );
        }
        else if(type && type.name === 'range')
        {
            value = value || [];
            var valueStart = _.get(value, '0');
            var valueEnd = _.get(value, '1');
            dateField = (
                <div className="form-group-inline">
                    <div className="form-group-row">
                        <div className="form-group-inline form-group-inline-daterange">
                            <DateField value={valueStart} onChange={this.onStartDateChange} />
                        </div>
                        <div className="form-group-inline form-group-label">
                            et
                        </div>
                        <div className="form-group-inline form-group-inline-daterange">
                            <DateField value={valueEnd} onChange={this.onEndDateChange} />
                        </div>
                    </div>
                </div>
            );
        }
        
        return (
            <div className="form-group form-group-filter-date form-group-row">
                <div className="form-group-inline form-group-label">
                    est
                </div>
                <div className="form-group-inline">
                    <button type="button" className="btn btn-default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        <span className="tag">{ typeLabel }</span>
                        <span className="glyphicon glyphicon glyphicon-menu-down"></span>
                    </button>
                    <ul className="dropdown-menu">
                        { types }
                    </ul>
                </div>
                { dateField }
            </div>
        );
    },
    
    renderTypes: function(types)
    {
        return types.map(_.bind(this.renderType, this));
    },
    
    renderType: function(type, index)
    {
        var key = 'type_'+index;
        
        var onClick = _.bind(function(e)
        {
            e.preventDefault();
            this.onTypeClick(type);
        }, this);
        
        return (
            <li key={key}>
                <a href="#" onClick={ onClick }>{ type.label }</a>
            </li>
        );
    },
    
    componentDidMount: function()
    {
        
    },
    
    onTypeClick: function(type)
    {
        if(type.name === 'future')
        {
            this.props.onChange('future');
        }
        else if(type.name === 'range')
        {
            this.props.onChange(['', '']);
        }
        else
        {
            this.props.onChange('');
        }
    },
    
    onDateChange: function(dateValue)
    {
        this.props.onChange(dateValue);
    },
    
    onStartDateChange: function(dateValue)
    {
        var value = _.get(this.props, 'value', null) || ['', ''];
        var currentValue = Immutable.fromJS(value);
        var newValue = currentValue.set(0, dateValue);
        
        if(currentValue !== newValue)
        {
            value = newValue.toJS();
            this.props.onChange(value);
        }
    },
    
    onEndDateChange: function(dateValue)
    {
        var value = _.get(this.props, 'value', null) || ['', ''];
        var currentValue = Immutable.fromJS(value);
        var newValue = currentValue.set(1, dateValue);
        
        if(currentValue !== newValue)
        {
            value = newValue.toJS();
            this.props.onChange(value);
        }
    }

});

module.exports = FilterDateField;



