var React = require('react-external');

var OrganisationItem = require('../items/Organisation.react');

var OrganisationsList = React.createClass({
    
    mixins: [Panneau.Mixins.List],
    
    getDefaultProps: function() {
        return {
            
        };
    },

    render: function()
    {
        var items = this.renderListItems(this.props.items);
        
        return (
            <div className="list list-rows list-organisations">
                { items }
            </div>
        );

    },
    
    getListItemComponent: function()
    {
        return OrganisationItem;
    }

});

module.exports = OrganisationsList;



