var React = require('react-external');

var ScreenItem = React.createClass({
    
    getDefaultProps: function() {
        return {
            
        };
    },

    render: function()
    {
        var name = _.get(this.props, 'data.snippet.title');
        var description = _.get(this.props, 'data.snippet.description');
        var id = _.get(this.props, 'data.id');
        var online = _.get(this.props, 'data.online', false);
        var link = URL.route('organisation.screens.show', {
            screen_id: id
        });
        
        var offline = null;
        if(!online)
        {
            offline = (
                <span className="banner-offline">Hors ligne</span>
            );
        }
        
        if(description && description.length)
        {
            description = (
                <span className="small">
                    { description }
                </span>
            );
        }
        else
        {
            description
        }
        
        var infos = null;
        var infosLabel = [];
        var vendor = _.get(this.props, 'data.vendor');
        var size = _.get(this.props, 'data.size');
        if(vendor && vendor.length)
        {
            infosLabel.push(vendor);
        }
        if(size && size.length)
        {
            infosLabel.push(size);
        }
        if(infosLabel.length)
        {
            infos = (
                <span className="small">
                    { infosLabel.join(' - ') }
                </span>
            );
        }
        
        var iconClassName = 'glyphicon glyphicon-'+(online ? 'ok':'remove')+'-sign';
        
        return (
            <div className="list-item list-item-sm">
                <a href={ link } className="thumbnail">
                    { offline }
                    <span className="icon">
                        <span className={iconClassName}></span>
                    </span>
                    <span className="caption caption-lg">
                        <strong>{ name }</strong>
                        { description }
                        { infos }
                    </span>
                </a>
            </div>
        );

    }

});

module.exports = ScreenItem;



