var React = require('react-external');
var _ = require('lodash-external');
var Image = require('../../vendor/image');

var BubbleSnippet = React.createClass({
    
    getDefaultProps: function() {
        return {
            data: null
        };
    },

    render: function()
    {    
        var title = _.get(this.props, 'data.title');
        var description = _.get(this.props, 'data.description', '').split('\n');
        var subtitle = _.get(this.props, 'data.subtitle', '');
        var link = _.get(this.props, 'data.link');
        var image = _.get(this.props, 'data.picture_thumbnail.link', _.get(this.props, 'data.picture.link'));
        
        if(subtitle && subtitle.length)
        {
            subtitle = (
                <div className="subtitle">{ subtitle }</div>
            );
        }
        else
        {
            subtitle = null;
        }
        
        if(description && description.length)
        {
            var lines = _.map(description, function(text, index)
            {
                var key = 'description_'+index;
                return (
                    <div key={key}>{ text }</div>
                );
            });
            description = (
                <div className="description">
                    { lines }
                </div>
            );
        }
        else
        {
            description = null;
        }
        
        
        var img = (
            <div className="image"></div>
        );
        if(image && image.length)
        {
            img =  (
                <img src={image} className="image" />
            );
        }
        
        return (
            <div className="snippet snippet-with-thumbnail snippet-bubble">
                <div className="snippet-thumbnail">
                    {img}
                </div>
                <div className="snippet-caption">
                    <h4 className="title">{ title }</h4>
                    { subtitle }
                    { description }
                </div>
            </div>
        );

    }

});

module.exports = BubbleSnippet;



