var React = require('react-external');
var _ = require('lodash-external');
var Panneau = require('panneau');

var PasswordField = Panneau.Components.Fields.Password;

var PasswordChangeField = React.createClass({
    
    propTypes: {
        name: React.PropTypes.string,
        value: React.PropTypes.object,
        onChange: React.PropTypes.func
    },
    
    getDefaultProps: function()
    {
        return {
            withPosition: true
        };
    },
    
    getInitialState: function()
    {
        return {
            update: this.props.errors && this.props.errors.length ? true:false
        };
    },
    
    render: function()
    {
        var props = _.omit(this.props, ['label']);
        
        var label = null;
        if(this.props.label && this.props.label.length)
        {
            label = (
                <label htmlFor={this.props.name} className="control-label">{ this.props.label }</label>
            )
        }
        
        var link = null;
        if(!this.state.update)
        {
            link = (
                <div className="link">
                    <a href="#" onClick={this.onClick}>Changer votre mot de passe...</a>
                </div>
            );
        }
        
        var passwordField = null;
        if(this.state.update)
        {
            passwordField = (
                <div className="field">
                    <PasswordField {...props} label="Nouveau mot de passe" confirmationLabel="Confirmer le mot de passe" />
                </div>
            );
        }
        
        return (
            <div className="form-group form-group-password-change">
                { label }
                { link }
                { passwordField }
            </div>
        );
    },
    
    onClick: function(e)
    {
        e.preventDefault();
        
        this.setState({
            update: true
        });
    }
    
});

module.exports = PasswordChangeField;



