var React = require('react-external');
var Immutable = require('immutable-external');
var _ = require('lodash-external');
var $ = require('jquery-external');

var ModalMixins = Panneau.Mixins.Modal;
var SubFormMixins = require('../../mixins/SubForm.react');

var Popover = Panneau.Components.Modals.Popover;
var FormActions = Panneau.Actions.Form;
var Requests = require('../../requests/index');
var TeamActions = require('../../actions/TeamActions');

var DangerZone = require('../DangerZone.react');

var TeamEditModal = React.createClass({
    
    mixins: [SubFormMixins],
    
    propTypes: {
        data: React.PropTypes.object.isRequired
    },
    
    getDefaultProps: function()
    {
        return {
            visible: false,
            element: null,
            offsetX: 0,
            offsetY: 0,
            formName: 'organisation.team.edit'
        };
    },
    
    getInitialState: function()
    {
        return {
            formData: Immutable.fromJS({
                role_id: _.get(this.props, 'data.role.id','')+''
            }),
            formErrors: null
        };
    },

    render: function()
    {
        var other = _.omit(this.props, ['role', 'onClose', 'onOpen']);
        var type = _.get(this.props, 'data.type', 'invitation');
        
        var title = type === 'inivtation' ? 'Modifier une invitation':'Modifer un membre de l\'quipe';
        var deleteTitle = '';
        var deleteDescription = type === 'inivtation' ? 'Supprimer l\'invitation':'Supprimer l\'utilisateur';
        var deleteConfirmation = type === 'invitation' ? 'tes-vous certain de vouloir supprimer cette invitation?':'tes-vous certain de vouloir enlever cet utilisateur de l\'organisation?';
        
        //Form
        var formProps = Panneau.Schema.Form[this.props.formName];
        formProps.name = this.props.formName;
        formProps.onSubmit = this.onSubmit;
        var formContainer = this.renderForm(formProps);
        
        return (
            <Popover title={title} {...other} onClose={this.onClose}>
                { formContainer }
                <DangerZone deleteTitle={deleteTitle} deleteDescription={deleteDescription} deleteConfirmation={deleteConfirmation} onDelete={this.onDelete} />
            </Popover>
        );

    },
    
    onClose: function()
    {
        if(this.props.onClose)
        {
            this.props.onClose();
        }
    },
    
    onSubmit: function(e)
    {
        e.preventDefault();
        
        if(!this.state.formData)
        {
            this.setState({
                formErrors: Immutable.fromJS({
                    'role_id': ['']
                })
            });
            return 
        }
        
        var type = _.get(this.props, 'data.type', 'invitation');
        var role_id = this.state.formData.get('role_id') || '';
        var onUpdated = _.bind(this.onUpdated, this);
        var onUpdateError = _.bind(this.onUpdateError, this);
        
        if(type === 'invitation')
        {
            Requests.Organisations.updateInvitation(this.props.data.id, role_id)
                    .then(onUpdated, onUpdateError);
        }
        else
        {
            Requests.Organisations.updateUser(this.props.data.id, role_id)
                    .then(onUpdated, onUpdateError);
        }
    },
    
    onDelete: function(e)
    {
        e.preventDefault();
        
        var type = _.get(this.props, 'data.type', 'invitation');
        var onRemoved = _.bind(this.onRemoved, this);
        var onRemoveError = _.bind(this.onRemoveError, this);
        
        if(type === 'invitation')
        {
            Requests.Organisations.removeInvitation(this.props.data.id)
                    .then(onRemoved, onRemoveError);
        }
        else
        {
            Requests.Organisations.removeUser(this.props.data.id)
                    .then(onRemoved, onRemoveError);
        }
    },
    
    onUpdated: function(data)
    {
        this.props.closeModal();
        
        data.type = this.props.data.type;
        
        if(this.props.onComplete)
        {
            this.props.onComplete(data);
        }
    },
    
    onUpdateError: function(errors)
    {
        this.updateFormErrors(errors);
    },
    
    onRemoved: function(user)
    {
        this.props.closeModal();
        
        if(this.props.onComplete)
        {
            this.props.onComplete(null);
        }
    },
    
    onRemoveError: function()
    {
        
    }

});

module.exports = TeamEditModal;



