var React = require('react-external');
var _ = require('lodash-external');
var Immutable = require('immutable-external');

var DateRangeField = Panneau.Components.Fields.DateRange;

var ConditionTimeField = React.createClass({

    propTypes: {
        name: React.PropTypes.string
    },
    
    getDefaultProps: function()
    {
        return {
            name: 'condition_time',
            value: null,
            onChange: null
        };
    },

    render: function()
    {
        var value = _.get(this.props, 'value', null);
        var checked = value ? true:false;
        
        var start = _.get(value || [], '0', null);
        var end = _.get(value || [], '1', null);
        var fieldValue = start || end ? {
            start: start,
            end: end
        }:null;
        
        var field = null;
        if(value)
        {
            field = (
                <DateRangeField type="time" name={this.props.name} value={fieldValue} onChange={this.onChange} />
            );
        }
        
        return (
            <div className="form-group form-group-condition form-group-condition-time">
                
                <div className="form-group form-group-checkbox">
                    <div className="checkbox">
                        <label>
                            <input type="checkbox" autoComplete="off" checked={checked} onChange={this.onCheckChange}/>
                            <span>Afficher seulement dans les heures</span>
                        </label>
                    </div>
                </div>
                
                { field }
            </div>
        );
    },
    
    onCheckChange: function(e)
    {
        if(e.target.checked)
        {
            this.onChange([]);
        }
        else
        {
            this.onChange(null);
        }
    },
    
    onChange: function(value)
    {
        var start = _.get(value, 'start', null);
        var end = _.get(value, 'end', null);
        
        value = start || end ? [start, end]:(value !== null ? []:null);
        
        if(this.props.onChange)
        {
            this.props.onChange(value);
        }
    }

});

module.exports = ConditionTimeField;



