var React = require('react-external');
var ReactDOM = require('react-dom-external');
var _ = require('lodash-external');
var $ = require('jquery-external');

var Modal = Panneau.Components.Modals.Modal;
var ModalActions = Panneau.Actions.Modal;

var Requests = require('../../requests/index');
var BubblesChannelFiltersList = require('../lists/BubblesChannelFilters.react');

var ChannelSettings = React.createClass({
    
    contextTypes: {
        store: React.PropTypes.object
    },
    
    getDefaultProps: function()
    {
        return {
            visible: false,
            screen: null,
            channel: null
        };
    },

    render: function()
    {
        
        var filters = _.get(this.props, 'channel.screen_settings.filters', null);
        
        var addButton;
        var canAddBubbles = _.get(this.props, 'channel.fields.settings.canAddBubbles', false);
        if(canAddBubbles)
        {
            addButton = (
                <button type="button" className="btn btn-primary pull-left" onClick={this.onClickAdd}>
                    <span className="glyphicon glyphicon-plus"></span> Ajouter du contenu
                </button>
            );
        }
        
        return (
            <Modal closeModal={this.props.closeModal} visible={this.props.visible} onClose={this.onClose}>
                <div className="modal-header">
                    <button type="button" className="close" onClick={this.onClickClose} aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                    <h4 className="modal-title">Modifier une chane</h4>
                </div>
                <div className="modal-body">
                    <BubblesChannelFiltersList 
                        screen={this.props.screen} 
                        channel={this.props.channel} 
                        filters={filters} 
                        onBubblesLoaded={this.onBubblesLoaded} 
                        onFiltersChange={this.onFiltersChange} 
                        onChannelChange={this.onChannelChange} />
                </div>
                <div className="modal-footer">
                    { addButton }
                    <button type="button" className="btn btn-default" onClick={this.onClickClose}>Fermer</button>
                </div>
            </Modal>
        );

    },
    
    resizeList: function()
    {
        var $el = $(ReactDOM.findDOMNode(this));
        
        var $header = $el.find('.modal-header');
        var $footer = $el.find('.modal-footer');
        var $children = $el.find('.bubbles-channel > div');
        var $list = $el.find('.list-bubbles');
        var windowHeight = $(window).height();
        var listHeight = windowHeight - $header.outerHeight() - $footer.outerHeight() - 100;
        $children.each(function()
        {
            if(!$(this).is('.list-bubbles') && !$(this).is('.loading-block'))
            {
                listHeight -= $(this).outerHeight();
            }
        })
        
        $list.css({
            'height': listHeight+'px'
        });
    },
    
    componentDidMount: function()
    {
        _.bindAll(this, 'onResize');
        $(window).on('resize', this.onResize);
        this.onResize();
    },
    
    componentWillUnmount: function()
    {
        $(window).off('resize', this.onResize);
    },
    
    componentDidUpdate: function()
    {
        this.resizeList();
    },
    
    onFiltersChange: function()
    {
        this.resizeList();
    },
    
    onBubblesLoaded: function()
    {
        this.resizeList();
    },
    
    onChannelChange: function(channel)
    {
        if(this.props.onChannelChange)
        {
            this.props.onChannelChange(channel);
        }
    },
    
    onResize: function()
    {
        this.resizeList();
    },
    
    onOpen: function()
    {
        this.resizeList();
        
        if(this.props.onOpen)
        {
            this.props.onOpen();
        }
    },
    
    onClickAdd: function(e)
    {
        e.preventDefault();
        
        this.context.store.dispatch(ModalActions.closeModal());
        
        /*this.props.closeModal();
        
        this.context.store.dispatch(ModalActions.openModal('BubbleForm', {
            onComplete: this.onAddModalComplete,
            onClose: this.onAddModalClose
        }));*/
    },
    
    onClickClose: function()
    {
        if(this.props.closeModal)
        {
            this.props.closeModal();
        }
        else
        {
            this.close();
        }
    },
    
    onClose: function()
    {
        this.context.store.dispatch(ModalActions.openModal('BubbleForm', {
            onComplete: this.onAddModalComplete,
            onClose: this.onAddModalClose
        }));
        
        if(this.props.onClose)
        {
            this.props.onClose();
        }
    },
    
    onAddModalComplete: function()
    {
        
    },
    
    onAddModalClose: function()
    {
        
    }

});

module.exports = ChannelSettings;



